import React, { Component } from 'react';
import logo from "../../uberdelta-logo.svg";

class LandingPage extends Component {

    render() {

		const mainStyle={
			border: "2px solid #99ce32",
			margin: "5% auto",
			width:"600px",
			background: "#f1f1f1",
			padding: "20px",
            textAlign:"center"
		}

        const headingStyle={
            fontWeight:"700px",
            fontSize:"40px",

        }

        return(
            <div style={mainStyle}>
            <img src={logo} alt="UberDelta logo"/>
			<br />
			<br />
            <div style={headingStyle}>404</div>
            Requested URL not found.
			</div>
            );
    }
}
export default LandingPage;



