const Web3Utility = require('./utility.js');
const BigNumber = require('bignumber.js');
const ABIOtherDelta = require('./contracts/uberdelta.json');
const ABIToken = require('./contracts/token.json');
//const ethUtil = require('ethereumjs-util');
//const Tx = require('ethereumjs-tx');
//const sigUtil = require('eth-sig-util')

var format = require('string-format')
format.extend(String.prototype);

function Service() {
  const self = this;

  self.init = (config) =>{

    const defaultGasPrice=4;

    self.config = config;
    self.web3 = config.web3;
    self.state = {
      orders: undefined,
      trades: undefined,
      myOrders: undefined,
      myTrades: undefined,
      currentBlock:undefined
    };
    if(self.web3!==null){
        self.contractOtherDelta = self.web3.eth.contract(ABIOtherDelta).at(config.addressOtherDelta);
        self.contractToken = self.web3.eth.contract(ABIToken).at(config.addressToken);
        self.setGasPrice(defaultGasPrice);
        self.utility=null;
        self.state.takeOrderGas=125000;
    }
  }

  self.setGasPrice=(newPrice)=>{
    newPrice=self.web3.toWei(newPrice,"gwei")
    self.state.gasPrice=newPrice;
    if(typeof self.utility!=="undefined" && self.utility!=null){
        self.utility.setGasPrice(newPrice);
    }
  }

  self.setUtility = (user,token) =>{
    self.utility = new Web3Utility(self.web3,user,token,self.contractOtherDelta,self.config.addressOtherDelta,self.contractToken);

  }

  self.getData = (type,token,user) => new Promise((resolve, reject) => {
    var url = null;
    switch (type){
        case "allTrades":
            url = self.config.apiURL + "/trades/{0}/0".format(token.addr);     
            break;
        case "userTrades":
            url = self.config.apiURL + "/myTrades/{0}/{1}/0".format(user,token.addr);     
            break;
        case "allOrders":
            url = self.config.apiURL + "/orders/{0}/0".format(token.addr);     
            break;
        case "userOrders":
            url = self.config.apiURL + "/myOrders/{0}/{1}/0".format(user,token.addr);     
            break;
        default:
            reject("Uknown getData type '{}' requested".format(type));
    }
    //console.log(url);
    function sleeper(ms) {
      return function(x) {
          return new Promise(resolve => setTimeout(() => resolve(x), ms));
    };
    };
    const delay=0;
    const startTime=performance.now();
    fetch(url).then(function(response){
        return response.json();
    }).then(sleeper(delay)).then((data) => {
        const latency=performance.now()-startTime;
        //console.log(url);
        //console.log(latency);
        var out={};
        out[type]=latency;
        resolve([data,out]);
    }).catch(function(err) {
        reject(err);
    });

    setTimeout(() => {
        reject("Unable to getData for {0} with url {1}".format(type,url));
    },5000+delay);

  });

  self.getOrderBookData = (token, user) => new Promise((resolve, reject) => {
    setTimeout(() => {
      reject('Could not get market');
    }, 20000);
    self.state = {
      orders: {buys:[],sells:[]},
      trades: [],
      myOrders: {buys:[],sells:[]},
      myTrades: [],
    };
    //if user is null (not passed in), then do not get any user specific data
    if(user)
    {
        Promise.all([self.getData("allTrades",token,user),self.getData("userTrades",token,user),self.getData("allOrders",token,user),self.getData("userOrders",token,user)]).then(data => {

            const [allTrades,userTrades,allOrders,userOrders] = data.map(function(value){return value[0]});
            const latencies = data.map(function(value){return value[1]}).reduce(function(out,obj){
            out[Object.keys(obj)[0]]=Object.values(obj)[0];
            return out;
            }); 

            self.state.trades=self.transformTrades(allTrades,token);
            self.state.myTrades=self.transformTrades(userTrades,token);

            var transformedOrders=self.transformOrders(allOrders,token);
            self.state.orders.buys=transformedOrders.buys;
            self.state.orders.sells=transformedOrders.sells;

            transformedOrders=self.transformOrders(userOrders,token);
            self.state.myOrders.buys=transformedOrders.buys;
            self.state.myOrders.sells=transformedOrders.sells;

            self.sortOrders();
            self.sortTrades();
            resolve(latencies);
        }).catch(err=>{
            reject(err);
        });
    }
    else
    {
        Promise.all([self.getData("allTrades",token,user),self.getData("allOrders",token,user)]).then(data => {

            self.state.trades=self.transformTrades(data[0],token);

            var transformedOrders=self.transformOrders(data[1],token);
            self.state.orders.buys=transformedOrders.buys;
            self.state.orders.sells=transformedOrders.sells;

            self.sortOrders();
            self.sortTrades();

            resolve();
        }).catch(err=>{
            reject(err);
        });
    }
  });

  self.getWeb3Data = (token,user) => new Promise((resolve,reject) => {
    Promise.all([
        self.getEthBalance(user),
        self.getTokenBalance(user),
        self.getOtherDeltaBalance('ETH', user),
        self.getOtherDeltaBalance(token, user),
        self.getTradeFee(user),
        self.getBlockNumber()
      ]).then(data => {
        resolve(data);
    });
  });

  self.transformOrders = (newOrders, token) => {
    const newOrdersTransformed = {
      buys: newOrders.buys
        .filter(x => x.takerTokenAddress.toLowerCase() === token.addr.toLowerCase())
        .map(x =>
        Object.assign({}, {
          id: x.id,
          date: new Date(x.updated),
          price: new BigNumber(x.price),
          takerTokenAmount: new BigNumber(x.takerTokenAmount),
          makerTokenAmount: new BigNumber(x.makerTokenAmount),
          amountFilled: new BigNumber(x.amountFilled),
          amountRemaining:new BigNumber(x.availableVolume),
          amountRemainingEth:new BigNumber(x.availableVolumeBase),
          tradeExpires: Number(x.tradeExpires),
          salt: x.salt,
          takerTokenAddress: x.takerTokenAddress,
          makerTokenAddress: x.makerTokenAddress,
          maker: x.maker,
          restrictedTo: x.restrictedTo,
          r: x.r,
          s: x.s,
          v: x.v ? Number(x.v) : undefined,
          amount: self.toEth(x.takerTokenAmount, token.decimals).toNumber(),
          amountBase: self.toEth(x.makerTokenAmount, 18).toNumber(),
          availableVolume: Number(x.availableVolume),
          ethAvailableVolume: Number(x.ethAvailableVolume),
          availableVolumeBase: Number(x.availableVolumeBase),
          ethAvailableVolumeBase: Number(x.ethAvailableVolumeBase),
        })),
      sells: newOrders.sells
      .filter(x => x.makerTokenAddress.toLowerCase() === token.addr.toLowerCase())
      .map(x =>
        Object.assign({}, {
          id: x.id,
          date: new Date(x.updated),
          price: new BigNumber(x.price),
          takerTokenAmount: new BigNumber(x.takerTokenAmount),
          makerTokenAmount: new BigNumber(x.makerTokenAmount),
          amountFilled: new BigNumber(x.amountFilled),
          amountRemaining:new BigNumber(x.availableVolume),
          amountRemainingEth:new BigNumber(x.availableVolumeBase),
          tradeExpires: Number(x.tradeExpires),
          salt: x.salt,
          takerTokenAddress: x.takerTokenAddress,
          makerTokenAddress: x.makerTokenAddress,
          maker: x.maker,
          restrictedTo: x.restrictedTo,
          r: x.r,
          s: x.s,
          v: x.v ? Number(x.v) : undefined,
          amount: self.toEth(x.makerTokenAmount, token.decimals).toNumber(),
          amountBase: self.toEth(x.takerTokenAmount, 18).toNumber(),
          availableVolume: Number(x.availableVolume),
          ethAvailableVolume: Number(x.ethAvailableVolume),
          availableVolumeBase: Number(x.availableVolumeBase),
          ethAvailableVolumeBase: Number(x.ethAvailableVolumeBase),
        })),
    };
    return newOrdersTransformed;
  };

  self.sortOrders = () =>{
    self.state.orders = {
      sells: self.state.orders.sells.sort((a, b) =>
        a.price - b.price || a.takerTokenAmount - b.takerTokenAmount),
      buys: self.state.orders.buys.sort((a, b) =>
        b.price - a.price || b.takerTokenAmount - a.takerTokenAmount),
    };
    self.state.myOrders = {
      sells: self.state.myOrders.sells.sort((a, b) =>
        a.price - b.price || a.takerTokenAmount - b.takerTokenAmount),
      buys: self.state.myOrders.buys.sort((a, b) =>
        b.price - a.price || b.takerTokenAmount - a.takerTokenAmount),
    };
  };

  self.transformTrades = (newTrades, token) => {
    const newTradesTransformed = newTrades
    .filter(x => !x.tokenAddr || x.tokenAddr.toLowerCase() === token.addr.toLowerCase())
    .map(x =>
      Object.assign(x, {
        txHash: x.txHash,
        tokenAddr: x.tokenAddr,
        side: x.side,
        date: new Date(x.date),
        amount: Number(x.amount),
        amountBase: Number(x.amountBase),
        price: Number(x.price),
        buyer: x.buyer,
        seller: x.seller,
      }));
    return newTradesTransformed;
  };

  self.sortTrades = () =>{
    self.state.trades = self.state.trades
      .sort((a, b) => new Date(b.date) - new Date(a.date) || b.amount - a.amount);
    self.state.myTrades = self.state.myTrades
      .sort((a, b) => new Date(b.date) - new Date(a.date) || b.amount - a.amount);
  };

  self.printOrderBook = () => {
    console.log('Order book');
    const ordersPerSide = 10;
    const sells = self.state.orders.sells.slice(0, ordersPerSide).reverse();
    const buys = self.state.orders.buys.slice(0, ordersPerSide);
    sells.forEach((order) => {
      console.log(`${order.price.toFixed(9)} ${order.ethAvailableVolume.toFixed(3)}`);
    });
    if (buys.length > 0 && sells.length > 0) {
      console.log(`---- Spread (${(sells[sells.length - 1].price - buys[0].price).toFixed(9)}) ----`);
    } else {
      console.log('--------');
    }
    buys.forEach((order) => {
      console.log(`${order.price.toFixed(9)} ${order.ethAvailableVolume.toFixed(3)}`);
    });
  };

  self.printTrades = () => {
    console.log('Recent trades');
    const numTrades = 10;
    self.state.trades.slice(0, numTrades).forEach((trade) => {
      console.log(`${trade.date} ${trade.side} ${Math.abs(trade.amount).toFixed(3)} @ ${trade.price.toFixed(9)}`);
    });
  };

  self.getOtherDeltaBalance = (token, user) => new Promise((resolve, reject) => {
    self.contractOtherDelta.balanceOf(token === 'ETH' ? '0x0000000000000000000000000000000000000000' : token.addr, user, (err, result) => {
      if (err) reject(err);
      resolve(result);
    });
  });

  self.getTradeFee = (user) => new Promise((resolve, reject) => {
    self.contractOtherDelta.userClass((user),(err,userClass)=>{
        if (err){
            reject(err);
        }
        else{
            self.contractOtherDelta.feeByClass(userClass,(err, result) => {
              if (err) reject(err);
              resolve(self.toEth(result.toNumber(),18));
            });
        }
    });
  });

  self.getEthBalance = (user) => new Promise((resolve, reject) => {
      self.web3.eth.getBalance(user, (err, result) => {
        if (err) reject(err);
        resolve(result);
    });
  });
  self.getTokenBalance = (user) => new Promise((resolve, reject) => {
      self.contractToken.balanceOf(user, (err, result) => {
        if (err) reject(err);
        resolve(result);
    });
  });

  self.getBlockNumber = () => new Promise((resolve, reject) => {
    self.web3.eth.getBlockNumber((err, result) => {
      if (err) reject(err);
      self.state.currentBlock=result;
      resolve(result);
    });
  });

  self.getNextNonce = user => new Promise((resolve, reject) => {
    self.web3.eth.getTransactionCount(user, (err, result) => {
      if (err) reject(err);
      resolve(result);
    });
  });

  self.cancelOrder = (user, order) => new Promise((resolve, reject) => {
    self.getNextNonce(user)
    .then((nonce) => {
      const addressData=[order.takerTokenAddress,order.makerTokenAddress,order.maker,order.restrictedTo];
      const numberData=[order.takerTokenAmount,order.makerTokenAmount,order.tradeExpires,order.salt];
      const args=[addressData,numberData];
      const data = self.contractOtherDelta.cancelOrder.getData(...args);
      const options = {
        gasPrice: self.state.gasPrice,
        gas: self.state.cancelOrderGas,
        nonce,
        data,
        to: self.config.addressOtherDelta,
      };
      self.web3.eth.defaultAccount = self.web3.eth.accounts[0]
      self.web3.eth.sendTransaction(options, (err, result) => {
        if (err) reject(self.handleMMError(err.toString()));
        resolve(result);
      });
    })
    .catch((err) => {
      reject(err);
    });
  });

  self.takeOrder = (user, order, fraction,noPartialFills) => new Promise((resolve, reject) => {
    self.getNextNonce(user)
    .then((nonce) => {
      var amount = order.takerTokenAmount.times(new BigNumber(String(fraction)));
      const addressData=[order.takerTokenAddress,order.makerTokenAddress,order.maker,order.restrictedTo];
      const numberData=[order.takerTokenAmount,order.makerTokenAmount,order.tradeExpires,order.salt];
      const args=[addressData,numberData,order.v,order.r,order.s,amount,noPartialFills];
      self.contractOtherDelta["testTrade"].call(
         addressData,numberData,order.v,order.r,order.s,amount,order.maker,noPartialFills,
         (errTest, resultTest) => {
          // eslint-disable-next-line
          if ((errTest))
          {
            reject("There was an error calling the testTrade function with MetaMask. Please ensure your MetaMask account is unlocked and functional or try again later please");
          }
          else if (noPartialFills&&resultTest.eq(0) )
          {
            reject("Cannot submit order because the available order quantity is below the FOK quantity. Allow partial fills in your order or select another order to fill");
          }
          else if (resultTest.eq(0))
          {
            reject("testTrade call failed. Please ensure you have the required balance. If you are sure you do, the order cannot be executed due to counter-party balance or due to the order being invalid.");

          }
          else{
              if(!noPartialFills&&amount>resultTest)
                amount=resultTest;
              const data = self.contractOtherDelta["trade"].getData(...args);
              const options = {
                gasPrice: self.state.gasPrice,
                gas: self.state.takeOrderGas,
                nonce,
                data,
                to: self.config.addressOtherDelta,
              };
              self.web3.eth.defaultAccount = self.web3.eth.accounts[0]
              self.web3.eth.sendTransaction(options, (err, result) => {
                if (err){
                    reject(self.handleMMError(err.toString()));
                }
                else{
                    resolve(result);
                }
              });
            }
        });
    })
    .catch((err) => {
      reject(err);
    });
  });

  self.placeOrder = order => new Promise((resolve, reject) => {
    var url = self.config.apiURL + "/message";
    //console.log(url);
    order.takerTokenAmount=order.takerTokenAmount.toString();
    order.makerTokenAmount=order.makerTokenAmount.toString();
    order.tradeExpires=order.tradeExpires.toString();
    order.salt=order.salt.toString();
    order=JSON.stringify(order);
    fetch(url,
    {
        method: "POST",
        body:order
    }).then(function(response){
        return response.json();
    }).then(function(data) {
        resolve(data);
    }).catch(function(err) {
        reject(err);
    });
  }); 

  self.createOrder = (side, tradeExpires, price, amount, token, user,callback) => {
    //amount and price should be BigNumber type to avoid floating point errors
    const sign = (typedData,cb) => {
		const from = user.addr
		const params = [typedData, user.addr]
        const method = 'eth_signTypedData'
        this.web3.currentProvider.sendAsync({method:method, params:params, from:from}, (err,result)=>{
            if(!err&&!result.error){
                cb(null,result.result);
            }
            else{
                cb(self.handleMMError(result.error.message),null);
            }
        });

    };

    if (side !== 'buy' && side !== 'sell') throw new Error('Side must be buy or sell');
    const amountBigNum = amount;
    const amountBaseBigNum = amount.times(price);
    const contractAddress = self.config.addressOtherDelta.toLowerCase();
    const takerTokenAddress = side === 'buy' ? token.addr : '0x0000000000000000000000000000000000000000';
    const makerTokenAddress = side === 'sell' ? token.addr : '0x0000000000000000000000000000000000000000';
    const takerTokenAmount = side === 'buy' ?
      self.toWei(amountBigNum, token.decimals) :
      self.toWei(amountBaseBigNum, 18);
    const makerTokenAmount = side === 'sell' ?
      self.toWei(amountBigNum, token.decimals) :
      self.toWei(amountBaseBigNum, 18);
    const orderSalt = Math.random().toString().slice(2);

    BigNumber.config({ EXPONENTIAL_AT: 1000 })
    const typedData = [
      {
        "name": "contractAddress",
        "type": "address",
        "value": contractAddress,
      },
      {
        "name": "takerTokenAddress",
        "type": "address",
        "value": takerTokenAddress,
      },
      {
        "name": "takerTokenAmount",
        "type": "uint256",
        "value": takerTokenAmount.toString(),
      },
      {
        "name": "makerTokenAddress",
        "type": "address",
        "value": makerTokenAddress,
      },
      {
        "name": "makerTokenAmount",
        "type": "uint256",
        "value": makerTokenAmount.toString(),
      },
      {
        "name": "tradeExpires",
        "type": "uint256",
        "value": tradeExpires.toString(),
      },
      {
        "name": "salt",
        "type": "uint256",
        "value": orderSalt,
      },
      {
        "name": "maker",
        "type": "address",
        "value": user.addr,
      },
      {
        "name": "restrictedTo",
        "type": "address",
        "value": "0x0000000000000000000000000000000000000000"
      }
    ];

    sign(typedData,(err,sig)=>{

        if(!err){
            /*const recovered= sigUtil.recoverTypedSignature({
                data: typedData,
                sig: sig
            });*/
            const orderObject = {
              takerTokenAmount,
              makerTokenAmount,
              takerTokenAddress,
              makerTokenAddress,
              contractAddress,
              tradeExpires,
              salt: orderSalt,
              maker: user.addr,
              restrictedTo: "0x0000000000000000000000000000000000000000",
              r: sig.substring(0,66),
              s: "0x"+sig.substring(66,130),
              v: parseInt(sig.substring(130,132),16),
            };
            const addressData=[orderObject.takerTokenAddress,orderObject.makerTokenAddress,orderObject.maker,orderObject.restrictedTo];
            const numberData=[orderObject.takerTokenAmount,orderObject.makerTokenAmount,orderObject.tradeExpires,orderObject.salt];
            const args=[addressData,numberData,orderObject.v,orderObject.r,orderObject.s];
              self.contractOtherDelta.availableVolume.call(
                ...args, 
                (errTest, resultTest) => {
                    console.log("hi");
                });

            callback(null,orderObject);
        }
        else{
            callback(err,null);
        }
    });
  };

    self.handleMMError = (err) =>{

        const deniedSig="Error: MetaMask Message Signature: User denied message signature.";
        const deniedSigIdx=err.indexOf(deniedSig);
        if(deniedSigIdx>-1){
            err=err.slice(deniedSigIdx,deniedSigIdx+deniedSig.length);
            return err;
        }

        const deniedTx="Error: MetaMask Tx Signature: User denied transaction signature.";
        const deniedTxIdx=err.indexOf(deniedTx);
        if(deniedTxIdx>-1){
            err=err.slice(deniedTxIdx,deniedTxIdx+deniedTx.length);
            return err;
        }

        return err
  };

  self.toEth = (wei, decimals) => new BigNumber(String(wei))
    .div(new BigNumber(10 ** decimals));
  self.toWei = (eth, decimals) => new BigNumber(String(eth))
    .times(new BigNumber(10 ** decimals)).floor();
}

module.exports = Service;



