import React, { Component } from 'react';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import './style.css';
//import 'react-tabs/style/react-tabs.css';
import Web3Utility from '../../utility.js';
import * as utils from "../../utils.js";

class Balance extends Component {

    constructor(props, context) {
        super(props, context);
        this.state={

            inputs:{
                deposit:{
                        tokenQuantity:"Quantity",
                        ethQuantity: "Quantity"
                },
                withdraw:{
                        tokenQuantity:"Quantity",
                        ethQuantity: "Quantity"
                },
                transfer:{
                        tokenQuantity:"Quantity",
                        ethQuantity: "Quantity",
                        tokenAddress : "Address",
                        ethAddress : "Address"
                }
            }
        }
        this.handleDeposit = this.handleDeposit.bind(this);
        this.handleWithdraw = this.handleWithdraw.bind(this);
        this.handleTransfer = this.handleTransfer.bind(this);
        this.setVal=this.setVal.bind(this);

    }

    handleChange = (id1,id2) => e => {
        this.setVal(id1,id2,e.target.value);
    }

    setVal = (id1,id2,val)=> {
        var tempInputs= this.state.inputs;
        tempInputs[id1][id2]=val;
        this.setState({inputs:tempInputs});
    }

    handleSubmit = (transactionType,id) => e => {
        e.preventDefault();
        var tokenType = id.indexOf("token")!==-1 ? "tkn" : "eth";
        var func=null;
        switch (transactionType){
            case 'deposit':
                func=this.handleDeposit;
                break;
            case 'withdraw':
                func=this.handleWithdraw;
                break;
            case "transfer":
                func=this.handleTransfer;
                break;
            default:
                console.log("ERROR unknown transaction type "+transactionType);
        }
        func(tokenType);
    }

    handleDeposit(tokenType){
        var qty=null;
        switch (tokenType){
            case 'tkn':
                qty=parseFloat(this.state.inputs.deposit.tokenQuantity);
                if(isNaN(qty)||qty<=0){
                    const msg="Must enter a nonnegative value greater than zero in order to deposit";
                    this.props.showAlert(utils.new_alert_details(
                                        "error",
                                        "Error",
                                        "The deposit transaction was not sent",
                                        msg
                    ));
                    return;
                }
                this.props.service.utility.deposit(qty,this.props.user,this.props.token,(err,res)=>{
                    if(!err){
                        const esLink="{}tx/{}".format(this.props.esUrl,res);
                        //console.log(esLink);
                        this.props.showAlert(utils.new_alert_details(
                                        "success",
                                        "Success",
                                        "The deposit transaction has been sent",
                                        "Successfully sent deposit tx {} {}.\nTx hash: {}".format(qty,this.props.token.name,res),
                                        [esLink,"EtherScan Link"]
                                        ));
                        this.setVal("deposit","tokenQuantity",0);
                    }
                    else{
                        this.props.showAlert(utils.new_alert_details(
                                        "error",
                                        "Error",
                                        "The deposit transaction was not sent",
                                        "Failed to send deposit with error: {}".format(err)
                        ));
                    }
                });
                break;
            case 'eth':
                qty=parseFloat(this.state.inputs.deposit.ethQuantity);
                if(isNaN(qty)||qty<=0){
                    const msg="Must enter a nonnegative value greater than zero in order to deposit"
                    this.props.showAlert(utils.new_alert_details(
                                        "error",
                                        "Error",
                                        "The deposit transaction was not sent",
                                        msg
                    ));
                    return;
                }
				this.props.service.utility.deposit(qty,this.props.user,Web3Utility.getEthToken(),(err,res)=>{
                    if(!err){
                        const esLink="{}tx/{}".format(this.props.esUrl,res);
                        //console.log(esLink);
                        this.props.showAlert(utils.new_alert_details(
                                        "success",
                                        "Success",
                                        "The deposit transaction has been sent",
                                        "Successfully sent deposit tx {} {}.\nTx hash: {}".format(qty,"ETH",res),
                                        [esLink,"EtherScan Link"]
                                        ));
                        this.setVal("deposit","ethQuantity",0);
                    }
                    else{
                        this.props.showAlert(utils.new_alert_details(
                                        "error",
                                        "Error",
                                        "The deposit transaction was not sent",
                                        "Failed to send deposit with error: {}".format(err)
                        ));
                    }
                });
                break;
            default:
                console.log("error did not expect tokenType "+tokenType);
        }
    }

    handleWithdraw(tokenType){
        var qty = null;
        switch (tokenType){
            case 'tkn':
                qty=parseFloat(this.state.inputs.withdraw.tokenQuantity);
                if(isNaN(qty)||qty<=0){
                    const msg="Must enter a nonnegative value greater than zero in order to withdraw";
                    this.props.showAlert(utils.new_alert_details(
                                        "error",
                                        "Error",
                                        "The deposit transaction was not sent",
                                        msg
                    ));
                    return;
                }
                this.props.service.utility.withdraw(qty,this.props.user,this.props.token,(err,res)=>{
                    if(!err){
                        const esLink="{}tx/{}".format(this.props.esUrl,res);
                        console.log(esLink);
                        this.props.showAlert(utils.new_alert_details(
                                        "success",
                                        "Success",
                                        "The withdraw transaction has been sent",
                                        "Successfully sent withdraw tx {} {}.\nTx hash: {}".format(qty,this.props.token.name,res),
                                        [esLink,"EtherScan Link"]
                                        ));
                        this.setVal("withdraw","tokenQuantity",0);
                    }
                    else{
                        this.props.showAlert(utils.new_alert_details(
                                        "error",
                                        "Error",
                                        "The withdraw transaction was not sent",
                                        "Failed to send withdraw with error: {}".format(err)
                        ));
                    }
                });
                break;
            case 'eth':
                qty=parseFloat(this.state.inputs.withdraw.ethQuantity);
                if(isNaN(qty)||qty<=0){
                    const msg="Must enter a nonnegative value greater than zero in order to withdraw";
                    this.props.showAlert(utils.new_alert_details(
                                        "error",
                                        "Error",
                                        "The deposit transaction was not sent",
                                        msg
                    ));
                    return;
                }
                this.props.service.utility.withdraw(qty,this.props.user,Web3Utility.getEthToken(),(err,res)=>{
                    if(!err){
                        const esLink="{}tx/{}".format(this.props.esUrl,res);
                        console.log(esLink);
                        this.props.showAlert(utils.new_alert_details(
                                        "success",
                                        "Success",
                                        "The withdraw transaction has been sent",
                                        "Successfully sent withdraw tx {} {}.\nTx hash: {}".format(qty,"ETH",res),
                                        [esLink,"EtherScan Link"]
                                        ));
                        this.setVal("withdraw","ethQuantity",0);
                    }
                    else{
                        this.props.showAlert(utils.new_alert_details(
                                        "error",
                                        "Error",
                                        "The withdraw transaction was not sent",
                                        "Failed to send withdraw with error: {}".format(err)
                        ));
                    }
                });
                break;
            default:
                console.log("error did not expect tokenType "+tokenType);
        }
    }

    handleTransfer(tokenType){
        switch (tokenType){
            case 'tkn':
                this.props.showAlert("Transfering "+this.state.inputs.transfer.tokenQuantity+" token to address "+this.state.inputs.transfer.tokenAddress );
                break;
            case 'eth':
                this.props.showAlert("Transfering "+this.state.inputs.transfer.ethQuantity+" eth to address "+this.state.inputs.transfer.ethAddress );
                break;
            default:
                console.log("error did not expect tokenType "+tokenType);
        }
    }

    onFocus() {
        this.setState({
          task: ''
        });
      }

    render() {
        const renderTable = (tokenName,user,customRowVal) =>(
            <table className="balanceTable" >
            <tbody>
                <tr className="deposit_header">
                    <th>Token</th>
                    <th>Wallet</th>
                    <th>UberDelta</th>
                </tr>
                <tr className="deposit_header">
                    {/* can we update balances bas
                    ed on subtracting  this.state.inputs.deposit.tokenQuantity } fronm balance? */}
                    <td><b>{tokenName}</b></td>
                    <td style={{padding:"0em 1em 0em 0em"}}>
                    <span className="no">{isNaN(user.tokenWalletBalance) ? "???" : user.tokenWalletBalance}</span></td>
                    <td><span className="no ud">{isNaN(user.tokenODBalance) ? "???" : user.tokenODBalance}</span></td>
                </tr>
                {customRowVal[0]}
                <tr className="deposit_header_sp">
                    <td><b>ETH</b></td>
                    <td><span className="no">{isNaN(user.ethWalletBalance) ? "???" : user.ethWalletBalance}</span></td>
                    <td><span className="no ud">{isNaN(user.ethODBalance) ? "???" : user.ethODBalance}</span></td>
                </tr>
                {customRowVal[1]}
            </tbody>
            </table>
        );

        var depositTab = [];
        var withdrawTab = [];
        var transferTab = [];
        

        depositTab.push((
        <tr>
        <td colSpan="4">
            <div className="deposit_header_caption">
            Deposit from your wallet to the smart contract:
            </div>
            <div className="input_order_grp" >
            <div className="input_order_box" >
                <input type="number" placeholder={this.state.inputs.deposit.tokenQuantity} value={this.state.inputs.deposit.tokenQuantity!=="Quantity"?this.state.inputs.deposit.tokenQuantity:""} onChange={this.handleChange("deposit","tokenQuantity")} onFocus={() => this.onFocus()} />
                <div className="max_buy_sm">
                {/* bind input to available balance */}
                <div className="btn_buy_max_sm">Deposit Max</div>
                </div>
                <span><div>Quantity {this.props.token.name}</div></span>
            </div>
            <input className="deposit_submit" type="submit" value = {'DEPOSIT ' + this.props.token.name} onClick={this.handleSubmit("deposit","tokenQuantity")} />
            </div>
        </td>
        </tr>
        ));
        depositTab.push((
            <tr>
            <td colSpan="4">
            <div className="input_order_grp" >
            <div className="input_order_box" >
              <input type="number" placeholder={this.state.inputs.deposit.ethQuantity} value={this.state.inputs.deposit.ethQuantity!=="Quantity"?this.state.inputs.deposit.ethQuantity:""} onChange={this.handleChange("deposit","ethQuantity")} />
              <div className="max_buy_sm">
                <div className="btn_buy_max_sm">Deposit Max</div>
              </div>
              <span>Quantity ETH</span>
            </div>
            <input className="deposit_submit" type="submit" value = "DEPOSIT ETH" onClick={this.handleSubmit("deposit","ethQuantity")}/>
          </div>
          </td>
          </tr>
        ));

        withdrawTab.push((
            <tr>
            <td colSpan="4">
                <div className="deposit_header_caption">
                Withdraw from smart contract to your wallet 
                </div>
                <div className="input_order_grp" >
                <div className="input_order_box" >
                <input type="number" placeholder={this.state.inputs.withdraw.tokenQuantity} value={this.state.inputs.withdraw.tokenQuantity!=="Quantity"?this.state.inputs.withdraw.tokenQuantity:""} onChange={this.handleChange("withdraw","tokenQuantity")} />
                    <div className="max_buy_sm">
                    {/* bind input to available balance */}
                    <div className="btn_withdraw_max_sm">Withdraw&nbsp;All</div>
                    </div>
                    <span><div>Quantity {this.props.token.name}</div></span>
                </div>
                <input className="deposit_submit" type="submit" value = {'WITHDRAW ' + this.props.token.name} onClick={this.handleSubmit("withdraw","tokenQuantity")}/>
                </div>
            </td>
            </tr>
        ));
        withdrawTab.push((
            <tr>
            <td colSpan="4">
                <div className="input_order_grp" >
                <div className="input_order_box" >
                <input type="number" placeholder={this.state.inputs.withdraw.ethQuantity} value={this.state.inputs.withdraw.ethQuantity!=="Quantity"?this.state.inputs.withdraw.ethQuantity:""} onChange={this.handleChange("withdraw","ethQuantity")}  />
                    <div className="max_buy_sm">
                    {/* bind input to available balance */}
                    <div className="btn_withdraw_max_sm">Withdraw&nbsp;All</div>
                    </div>
                    <span><div>Quantity ETH</div></span>
                </div>
                <input className="deposit_submit" type="submit" value = "WITHDRAW ETH" onClick={this.handleSubmit("withdraw","ethQuantity")}/>
                </div>
            </td>
            </tr>
        ));

        transferTab.push((
            <tr>
            <td colSpan="4">
                <div className="deposit_header_caption">
                Transfer from your account to another
                </div>
                <div className="input_order_grp" >
                <div className="input_order_box" >
                <input type="number" placeholder={this.state.inputs.transfer.tokenQuantity} onChange={this.handleChange("transfer","tokenQuantity")} />
                    <span><div>Call tokenName</div></span>
                </div>
                <div className="input_order_box" >
                <input type="text" placeholder={this.state.inputs.transfer.tokenAddress} onChange={this.handleChange("transfer","tokenAddress")} />
                    <span><div>Address</div></span>
                </div>
                <input class="withdraw_submit" type="submit" value = "TRANSFER" onClick={this.handleSubmit("transfer","tokenQuantity")}/>
                </div>
            </td>
            </tr>
        ));
        transferTab.push((
            <tr>
            <td colSpan="4">
    
                <div className="input_order_grp" >
                <div className="input_order_box" >
                <input type="number" placeholder={this.state.inputs.transfer.ethQuantity} onChange={this.handleChange("transfer","ethQuantity")}  />
                    <span><div>Call tokenName</div></span>
                </div>
                <div className="input_order_box" >
                <input type="text" placeholder={this.state.inputs.transfer.ethAddress} onChange={this.handleChange("transfer","ethAddress")} />
                    <span><div>Address</div></span>
                </div>
                <input class="withdraw_submit" type="submit" value = "TRANSFER" onClick={this.handleSubmit("transfer","ethQuantity")}/>
                </div>
            </td>
            </tr>
        ));

        const component = (
            <Tabs>
                <TabList>
                    <Tab>DEPOSIT</Tab>
                    <Tab>WITHDRAW</Tab>
                    {/* <Tab>TRANSFER</Tab> */}
                </TabList>

                <TabPanel>
                    {renderTable(this.props.token.name,this.props.user,depositTab)}
                </TabPanel>
                <TabPanel>
                    {renderTable(this.props.token.name,this.props.user,withdrawTab)}
                </TabPanel>
                {/*  <TabPanel>
                    {renderTable(this.props.token.name,this.props.user,transferTab)}
                </TabPanel>  */}
            </Tabs>
        );
        return (component);
    }
}
export default Balance;



