const SolidityFunction = require("web3/lib/web3/function.js");
const coder = require('web3/lib/solidity/coder.js');
const sha3 = require("web3/lib/utils/sha3.js");
const utils = require('web3/lib/utils/utils.js');
const BigNumber = require("bignumber.js");
const config = require("./config.json");

class Web3Utility{

	constructor(web3,user,token,contractOtherDelta,addressOtherDelta,contractToken){
		this.nonce=0;
		this.web3=web3;
		this.addressOtherDelta=addressOtherDelta;
		this.contractToken=contractToken;
		this.contractOtherDelta=contractOtherDelta;
		this.user=user;
		this.token=token;
		this.gasDeposit=125000;
		this.gasApprove=125000;	
        this.gasPrice=4000000000;
	}

    setGasPrice(newPrice){
        this.gasPrice=newPrice;
    }

    send(
        contract,
        address,
        functionName,
        argsIn,
        fromAddress,
        callback
    ){
        function encodeConstructorParams(abi, params) {
            return (
                abi
                    .filter(
                        json =>
                            json.type === "constructor" &&
                            json.inputs.length === params.length
                    )
                    .map(json => json.inputs.map(input => input.type))
                    .map(types => coder.encodeParams(types, params))[0] || ""
            );
        }

        // Processing arguments passed in (all the order information like takerTokenAddress, makerTokenAddress, etc...)
        const args = Array.prototype.slice
            .call(argsIn)
            .filter(a => a !== undefined);

        let options = {};

        if (typeof args[args.length - 1] === "object" && args[args.length - 1].gas) {
            args[args.length - 1].gasPrice = this.gasPrice;
            args[args.length - 1].gasLimit = args[args.length - 1].gas;
            delete args[args.length - 1].gas;
        }

        if (utils.isObject(args[args.length - 1])) {
            options = args.pop();
        }
        let nonce=this.nonce
        this.getNextNonceWeb3(fromAddress, (err, nextNonce) => {
            if (!err && nonce < nextNonce) {
                nonce = nextNonce;
            }
            //console.log("Order nonce:", nonce);
            options.nonce = nonce;
            if (functionName === "constructor") {
                if (options.data.slice(0, 2) !== "0x") {
                    options.data = `0x${options.data}`;
                }
                const encodedParams = encodeConstructorParams(contract.abi, args);
                //console.log(encodedParams);
                options.data += encodedParams;
            } else if (!contract || !functionName) {
                options.to = address;
            } else {
                options.to = address;
                const functionAbi = contract.abi.find(
                    element => element.name === functionName
                );
                const inputTypes = functionAbi.inputs.map(x => x.type);
                const typeName = inputTypes.join();
                options.data = `0x${sha3(`${functionName}(${typeName})`).slice(0, 8)}${coder.encodeParams(inputTypes, args)}`;
            }

            //console.log("Transaction to be sent:");
            //console.log(options.data.slice(0, 10)); // The method hashed
            /*let pos = 10;
            for (let i = 0; i < (options.data.length - 10) / 64; i++) {
                //console.log(options.data.slice(pos, pos+64));
                pos += 64;
            }*/

            try {
                options.from = fromAddress;
                options.gas = options.gasLimit;
                delete options.gasLimit;

                // Here we send the actual order
                this.web3.eth.sendTransaction(options, (errSend, hash) => {
                    if (!errSend) {
                        this.setNonce(nonce+1);
                        callback(null, {
                            txHash: hash,
                        });
                    } else {
                        callback("error in send transaction: {}".format(errSend),null);
                    }
                });
            }
            catch (errCatch) {
                callback(errCatch,null);
            }
        });
    }

     call(contract, address, functionName, args, callback) {
        const data = contract[functionName].getData.apply(null, args);
        this.web3.eth.call({ to: address, data }, (err, result) => {
            if (!err) {
                const functionAbi = contract.abi.find(
                    element => element.name === functionName
                );
                const solidityFunction = new SolidityFunction(
                    this.web3.Eth,
                    functionAbi,
                    address
                );
                try {
                    const resultUnpacked = solidityFunction.unpackOutput(
                        result
                    );
                    callback(null, resultUnpacked);
                } catch (errJson) {
                    callback(errJson,null);
                }
            } else {
                callback(err,null);
            }
        });
    }

     getNextNonceWeb3(address, callback){
        this.web3.eth.getTransactionCount(address, (err, result) => {
            if (!err) {
                const nextNonce = Number(result);
                // Note. initial nonce is 2^20 on testnet,
                // but getTransactionCount already starts at 2^20.
                callback(null, nextNonce);
            } else {
                console.log("error in getTransactionCount");
                callback(err,null);
            }
        });
    }

	 getBalance(address, callback){
		this.web3.eth.getBalance(address, (err, balance) => {
		if (!err) {
			callback(null, balance);
		} else {
			console.log("error in getBalance");
		}
		});

	}

    setNonce(newNonce){
        this.nonce=newNonce;
    }

    deposit(rawQuantity,user,token,callback) {
        const tokenDivisor=Web3Utility.tokenDivisor(token);
		var quantity=new BigNumber(Number(Web3Utility.ethToWei(rawQuantity, tokenDivisor)));
		if (token.addr.slice(0, 39) === "0x0000000000000000000000000000000000000") {
			this.getBalance(
                user.addr,
                (err, result) => {
                    if (quantity.lte(result)) {
                        this.send(
                            this.contractOtherDelta,
                            this.addressOtherDelta,
                            "deposit",
                            [
                                {
                                    gas: this.gasDeposit,
                                    value: quantity.toNumber()
                                }
                            ],
                            user.addr,
                            (errSend, resultSend) => {
                                if (errSend) {
                                    callback(this.handleMMError(errSend),null);
                                    return;
                                }
                                else {
                                    callback(null,resultSend.txHash);

                                }
                            }
                        );
                    }
                    else {
                        callback("Cannot deposit value {} greater than current eth balance {}".format(Web3Utility.weiToEth(quantity),Web3Utility.weiToEth(result)),null);
                    }
                }
            );
		}
		else{

			this.call(this.contractToken,this.token.addr,"allowance",[this.user.addr,this.addressOtherDelta], (errAllowance,resAllowance)=>{
					
                    this.call(this.contractToken,this.token.addr,"balanceOf",[this.user.addr], (errBalanceOf,resBalanceOf)=>{

						const sendDeposit = () =>{this.send(this.contractOtherDelta,this.addressOtherDelta,"depositToken",[this.token.addr,quantity,{gas:this.gasDeposit,value:0}],this.user.addr,(errDepositToken,resDepositToken) =>{
							if (!errDepositToken){
								callback(null,resDepositToken.txHash);
							}
							else{
								callback(this.handleMMError(errDepositToken),null);
							}
						})};
						if (quantity.lte(resBalanceOf)){
							if (resAllowance.lt(quantity)){

                                const getApproval = (callback) =>{
                                    if (this.token.tokenType===20)
                                    {
                                        this.send(this.contractToken,this.token.addr,"approve",[this.addressOtherDelta,quantity,{gas:this.gasApprove,value:0}],this.user.addr,(errApprove,resApprove) =>{callback(errApprove,resApprove)});
                                    }
                                    else{
                                        callback("Support for non-ERC20 has not been implemented yet",null);
                                    }
                                };

                                getApproval((errApprove,resApprove)=>{								
								if (!errApprove){
									sendDeposit();
								}
								else{
									callback(this.handleMMError(errApprove),null);
								}
								});
							}
							else{
								sendDeposit();
							}
						}
						else{
							callback("Cannot deposit value {} greater than current token balance {}".format(Web3Utility.weiToEth(quantity),Web3Utility.weiToEth(resBalanceOf)),null);
						}
					});

			});
		}
  	}

	withdraw(rawQuantity,user,token,callback) {
        const tokenDivisor=Web3Utility.tokenDivisor(token);
		var quantity=new BigNumber(Number(Web3Utility.ethToWei(rawQuantity, tokenDivisor)));
        this.call(this.contractOtherDelta,this.addressOtherDelta,"balanceOf",[token.addr,this.user.addr], (errBalanceOf,resBalanceOf)=>{
            if(errBalanceOf){
                callback("Error with getting balance of: {}".format(errBalanceOf),null);
            }
            if(quantity.gt(resBalanceOf)){
                callback("Cannot withdraw value {} greater than current contract balance {}".format(Web3Utility.weiToEth(quantity,tokenDivisor),Web3Utility.weiToEth(resBalanceOf,tokenDivisor)),null);
            }
            else{

                if (token.addr.slice(0, 39) === "0x0000000000000000000000000000000000000") {
                    this.send(
                        this.contractOtherDelta,
                        this.addressOtherDelta,
                        "withdraw",
                        [
                            quantity,
                            {
                                gas: this.gasDeposit,
                                value: 0
                            }
                        ],
                        user.addr,
                        (errWithdraw, resWithdraw) => {
                            if (errWithdraw) {
                                callback(this.handleMMError(errWithdraw),null);
                            }
                            else {
                                callback(null,resWithdraw.txHash);
                            }
                        }
                    );
                }
                else{
                    this.send(
                        this.contractOtherDelta,
                        this.addressOtherDelta,
                        "withdrawToken",
                        [
                            token.addr,
                            quantity,
                            {  
                                gas:this.gasDeposit,
                                value:0
                            }
                        ],
                        user.addr,
                        (errWithdraw,resWithdraw) =>{
                            if (errWithdraw) {
                                callback(this.handleMMError(errWithdraw),null);
                            }
                            else {

                                callback(null,resWithdraw.txHash);
                            }
                        }
                    );
                }
            }
        });
  	}

    handleMMError(err){

        const deniedSig="Error: MetaMask Message Signature: User denied message signature.";
        const deniedSigIdx=err.indexOf(deniedSig);
        if(deniedSigIdx>-1){
            err=err.slice(deniedSigIdx,deniedSigIdx+deniedSig.length);
            return err;
        }

        const deniedTx="Error: MetaMask Tx Signature: User denied transaction signature.";
        const deniedTxIdx=err.indexOf(deniedTx);
        if(deniedTxIdx>-1){
            err=err.slice(deniedTxIdx,deniedTxIdx+deniedTx.length);
            return err;
        }

        return err
  }

};    
Web3Utility.ethToWei=function(eth, divisorIn) {
        const divisor = !divisorIn ? 1000000000000000000 : divisorIn;
        return parseFloat((eth * divisor).toPrecision(10));
    }

Web3Utility.weiToEth=function(eth, divisorIn) {
        const divisor = !divisorIn ? 1000000000000000000 : divisorIn;
        return parseFloat((eth / divisor).toPrecision(10));
    }

Web3Utility.tokenDivisor=function(token) {
        return Math.pow(10, token.decimals)
    }

Web3Utility.getEthToken=function(){
		return config.tokens[0];
	}


module.exports =Web3Utility; 



