import React from 'react';
import NotificationSystem from 'react-notification-system';
import CustomElement from "./CustomElement";

const temporaryStyle = {
  display: 'inline-block',
  margin: 'auto',
  color: 'red',
  backgroundColor: 'black',
  fontWeight: 'bold',
  float: 'left'
};

class Notifications extends React.Component {

  constructor(props, context) {
    super(props, context)
    this._notificationSystem = null

    this.state={seenTxHashes:[]};

  }

  _addNotificationBlockchainEvent(bcEvent,txHash) {
    if (this._notificationSystem) {
        if (this.state.seenTxHashes.indexOf(txHash)<0){
          this
            ._notificationSystem
            .addNotification({title: 'Blockchain Event Detected', message: '{} event seen on blockchain with tx hash {}'.format(bcEvent,txHash), level: 'success', position: 'br', autoDismiss: 10});
          this.setState({seenTxHashes:this.state.seenTxHashes.concat([txHash])});
        }
    }
  }

  _addNotificationFailedTradeEvent(msg,txHash) {
    if (this._notificationSystem) {
        if (this.state.seenTxHashes.indexOf(txHash)<0){
          this
            ._notificationSystem
            .addNotification({title: 'Failed Trade Event Detected', message: 'Attempted trade with tx hash {} failed with reason: {}'.format(txHash,msg), level: 'error', position: 'br', autoDismiss: 0});
          this.setState({seenTxHashes:this.state.seenTxHashes.concat([txHash])});
        }
    }
  }

  _addNotificationSuccess(event) {
    event.preventDefault();
    if (this._notificationSystem) {
      this
        ._notificationSystem
        .addNotification({title: 'Success', message: 'Notification message', level: 'success', position: 'br', autoDismiss: 2});
    }
  }

  _addNotificationError(event) {
    event.preventDefault();
    if (this._notificationSystem) {
      this
        ._notificationSystem
        .addNotification({
          title: 'Error',
          message: 'Error Notification message',
          level: 'error',
          position: 'br',
          autoDismiss: 0, //force user to deal with
          action: {
            label: 'Ok',
            callback: function () {
              console.log('log message');
            }
          }
        });
    }
  }

  _addNotificationInfo(event) {
    event.preventDefault();
    if (this._notificationSystem) {
      this
        ._notificationSystem
        .addNotification({
          title: 'Info',
          message: 'Info Notification message',
          level: 'info',
          position: 'br',
          autoDismiss: 4,
          callback: function () {
            console.log('Notification button clicked!');
          }
        });
    }
  }

  _addNotificationWarning(event) {
    event.preventDefault();
    if (this._notificationSystem) {
      this
        ._notificationSystem
        .addNotification({
          title: 'Warning',
          message: 'Warning Notification message',
          level: 'warning',
          position: 'br',
          autoDismiss: 4,
          callback: function () {
            console.log('Warning button clicked!');
          }
        });
    }
  }
  // multiple
  _addNotificationCustom(event) {
    event.preventDefault();
    if (this._notificationSystem) {
      this
        ._notificationSystem
        .addNotification({
          title: 'Custom',
          message: 'Custom Notification message',
          level: 'info',
          position: 'br',
          autoDismiss: 4,
          children: (
            <div>
              <CustomElement name="Custom Element NAME"/>
            </div>
          ),
          action: {
            label: '',
            callback: function () {
              console.log('Warning message');
            }
          }
        });
    }
  }

  render() {
    return (
      <div style={temporaryStyle}>
        {/*this._addNotificationSuccess(null)*/}
        {/*<button onClick={this
          ._addNotificationSuccess
          .bind(this)}>Success notification</button>

        <button onClick={this
          ._addNotificationError
          .bind(this)}>Error notification</button>

        <button onClick={this
          ._addNotificationInfo
          .bind(this)}>Info notification</button>

        <button onClick={this
          ._addNotificationWarning
          .bind(this)}>Warning notification</button>

        <button onClick={this
          ._addNotificationCustom
          .bind(this)}>Custom notification</button>
        */}

        <NotificationSystem ref={n => this._notificationSystem = n}/>

      </div>
    );
  }
};

export default Notifications



