import React, { Component } from 'react';
import ReactResponsiveSelect from 'react-responsive-select';

import './style.css';

const caretIcon = (
    <svg className="caret-icon" x="0px" y="0px" width="11.848px" height="6.338px" viewBox="351.584 2118.292 11.848 6.338">
      <g><path d="M363.311,2118.414c-0.164-0.163-0.429-0.163-0.592,0l-5.205,5.216l-5.215-5.216c-0.163-0.163-0.429-0.163-0.592,0s-0.163,0.429,0,0.592l5.501,5.501c0.082,0.082,0.184,0.123,0.296,0.123c0.103,0,0.215-0.041,0.296-0.123l5.501-5.501C363.474,2118.843,363.474,2118.577,363.311,2118.414L363.311,2118.414z"/></g>
    </svg>
  );

class Token extends Component {

  constructor(props, context) {
    super(props, context);

    var options=this.props.tokenList.filter((x)=>{
        return x.name!=="ETH";
    })

    var count=0;
    while(count<0){
        var val=Object.assign({},options.slice(-1)[0]);
        val.name=Math.random().toString(36).substring(7);
        options.push(val);
        count++;
    }

    this.state={options:options};


  }

  reportChange = this.reportChange.bind(this);

  reportChange(newValue) {
    this.props.tokenChangeCallback(newValue);
  }

  render() {

    return (
      <form ref={r => this.form = r} style={{}}>

        <ReactResponsiveSelect
          name="make"
         
          options={this.state.options.map(
            function(x){
                return {
                    value:x.name,
                    text:x.name+"/ETH",
                    markup: <span> {x.name}<span className="hdr_integer_caption" aria-hidden="true">({x.name})</span></span>
                }
            }
          )}
          //onChange={this.reportChange}
          onChange={this.reportChange}
          onSubmit={() => { this.form.submit(); }}
          caretIcon={caretIcon}

          prefix=""
          selectedValue={this.props.token.name.toUpperCase()}
        />
        
      </form>
    );
  }
}

export default Token;



