import React, {Component} from 'react';
import './app.css';
import OrderBook from './components/OrderBook';
import Header from './components/Header';
import TradeHistory from './components/TradeHistory';
import Balance from './components/Balance';
import UserTransactions from './components/UserTransactions';
import PlaceOrders from './components/PlaceOrders';
import ReactModal from 'react-modal';
import './modal.css';
import Notifications from './components/Notifications';
import Cookies from 'universal-cookie';
import downloadMM from "./download-metamask-dark.png";
import config from './config.json';
import Slider from 'react-rangeslider';
import * as utils from "./utils.js";
import Gitter from './components/Gitter';

const Service = require('./api_od_rest');
const numeral = require('numeral');

class AppGrid extends Component{

  constructor(props, context) {
    super(props, context);

    const cookies = new Cookies();

    var userTemp ={};

    var Sidecar = require('gitter-sidecar');
    var myChat = new Sidecar({
      room: 'UberDelta/Lobby'
    });

    userTemp["ethWalletBalance"]=Number.NaN;
    userTemp["ethODBalance"]=Number.NaN;

    userTemp["tokenWalletBalance"]=Number.NaN;
    userTemp["tokenODBalance"]=Number.NaN;

    userTemp["orders"]={}
    userTemp["orders"]["buy"]=[];
    userTemp["orders"]["sell"]=[];
    userTemp["trades"]=[];

    userTemp["tradeFee"]=Number.NaN

    this.state = {
      orders:{
          buys: [],
          sells: []
      },
      cookies:cookies,
      trades:[],
      account:null,
      mmLoggedIn:false,
      token:this.props.token,
      tokenList:this.props.tokenList,
      addressOtherDelta:this.props.addressOtherDelta,
      esUrl:this.props.esUrl,
      user:userTemp,
      service:null,
      web3:null,
      orderBookStatus:"connecting",
      alertModal:{
                show:false,
                text:<b>hi</b>,
                buttonText:"Ok"
            },
      confirmTradeModal:{
                show:false,
                text:"",
                confirmed:false,
                callback:null,
                msgGen:null,
                noPartialFills:false,
                maxQuantity:NaN,
                quantity:NaN,
            },
      confirmOrderModal:{
                show:false,
                text:"",
                confirmed:false,
                callback:null,
            },
      confirmCancelModal:{
                show:false,
                text:"",
                confirmed:false,
                callback:null,
            },
      orderSide: ""  
  };

  console.log("Welcome! Loading data for token "+this.state.token.name);
  this.setupService = this.setupService.bind(this);
  this.updateService = this.updateService.bind(this);
  this.startService = this.startService.bind(this);
  //this.setToken = this.setToken.bind(this);
  this.setGasPrice = this.setGasPrice.bind(this);
  this.calcTokenDelta=this.calcTokenDelta.bind(this);
  this.showAlert=this.showAlert.bind(this);
  this.showConfirmTrade=this.showConfirmTrade.bind(this);
  this.showConfirmOrder=this.showConfirmOrder.bind(this);
  this.showConfirmCancel=this.showConfirmCancel.bind(this);
  this.onNoPartialFillsChange=this.onNoPartialFillsChange.bind(this);
  }

  componentWillReceiveProps(nextProps){
    this.setState({token:nextProps.token},()=>{
        if(this.state.web3==null)
        {
        this.setupServiceNoWeb3();
        }
        else
        {
        this.setupService();
        }
    });
  }

  componentDidMount(){

    if(!JSON.parse(sessionStorage.getItem("sawWarning")))
    {
        sessionStorage.setItem("sawWarning",true);
        this.showAlert([utils.new_alert_details(
                        ...config.welcomeObject),utils.mail_chimp_signup()],
                        "I understand",true);
    }

    /*if(typeof this.state.cookies.get("firstTime")==="undefined")
    {
        this.state.cookies.set("firstTime",false,{maxAge:1});
        this.showAlert([utils.new_alert_details(
                        ...config.welcomeObject),utils.mail_chimp_signup()],
                        "Trade Now");
    }*/
    var web3 = this.props.web3;
    if (web3) {
      // eslint-disable-next-line no-undef
        this.setState({web3:web3});
        web3.version.getNetwork((err, netId) => {
          /*switch (netId) {
            case "1":
              console.log('This is mainnet')
              break
            case "2":
              console.log('This is the deprecated Morden test network.')
              break
            case "3":
              console.log('This is the ropsten test network.')
              break
            case "4":
              console.log('This is the Rinkeby test network.')
              break
            case "42":
              console.log('This is the Kovan test network.')
              break
            default:
              console.log('This is an unknown network.')
          }*/
            if(netId!==config.network.mmValue)
            {
                this.showAlert("MetaMask detected but the wrong network is currently active. Please make sure you are using the {}.".format(config.network.name));
            }
            else
            {
                var noAccountAlert=true;
                setInterval(()=>{
                    const prevAccount=this.state.account;
                    web3.eth.getAccounts((err, accounts)=> { 
                      if (accounts.length>0){
                        noAccountAlert=true;
                        if(prevAccount!==accounts[0]){
                              var userTemp=this.state.user;
                              userTemp["addr"]=accounts[0];
                              this.setState({
                                  account:accounts[0],
                                  user:userTemp,
                                  mmLoggedIn:true
                              },this.setupService);
                          }
                      }
                      else{
                        if(noAccountAlert){
                            this.showAlert("MetaMask detected but no accounts unlocked. Please unlock an account to unlock trading functionality");
                            noAccountAlert=false;
                        }
                      }
                    });
                },100);
            }
        });
    }
    else{
        this.showAlert("Could not detect a MetaMask installation. Please download and setup a new account in order to unlock trading functionality");
        this.setupServiceNoWeb3();
    }

  }

  setupService(){
    var token=this.state.token
    const apiConfig = {
      addressOtherDelta: this.state.addressOtherDelta,
      addressToken:token.addr,
      web3 : this.state.web3,
      apiURL:config.apiURL
    };

    const service = new Service();
    service.init(apiConfig);
    this.setState({
        service:service
    },()=>{this.startService();});

    service.setUtility({"addr":this.state.user.addr},token);

    service.contractOtherDelta.Withdraw({"user":this.state.user.addr},(error,withdrawEvent)=>{
        console.log(withdrawEvent);
        this.updateService();
        this.notifier._addNotificationBlockchainEvent("Withdraw",withdrawEvent.transactionHash);
    });
    service.contractOtherDelta.Deposit({"user":this.state.user.addr},(error,depositEvent)=>{
        console.log(depositEvent);
        this.updateService();
        this.notifier._addNotificationBlockchainEvent("Deposit",depositEvent.transactionHash);
    });
    service.contractOtherDelta.Trade({"user":this.state.user.addr},(error,tradeEvent)=>{
        console.log(tradeEvent);
        this.updateService();
        this.notifier._addNotificationBlockchainEvent("Trade",tradeEvent.transactionHash);
    });
    service.contractOtherDelta.FailedTrade({"user":this.state.user.addr},(error,failedTradeEvent)=>{
        console.log(failedTradeEvent);
        this.updateService();
        var msg;
        switch(failedTradeEvent.args.failReason.toNumber()){
            case 1:
                msg="Compliance Restriction";
                break;
            case 2:
                msg="Cannot trade more than available balance";
                break;
            case 3:
                msg="Cannot take trade with restricted counter-party";
                break;
            case 4:
                msg="Order has expired";
                break;
            case 5:
                msg="Not a valid order";
                break;
            case 6:
                msg="Could not fill full quantity in FOK order";
                break;
            case 7:
                msg="Order previously filled or cancelled";
                break;
            default:
                msg="Unknown error occured";
                break;

        }
        this.notifier._addNotificationFailedTradeEvent(msg,failedTradeEvent.transactionHash);
    });
    service.contractOtherDelta.Cancel((error,cancelEvent)=>{
        console.log(cancelEvent);
        this.updateService();
        this.notifier._addNotificationBlockchainEvent("Cancel",cancelEvent.transactionHash);
    });
}

  setupServiceNoWeb3(){
    var token=this.state.token
    const apiConfig = {
      addressOtherDelta: this.state.addressOtherDelta,
      addressToken:token.addr,
      web3 : null,
      apiURL: config.apiURL
    };

    const service = new Service();
    service.init(apiConfig);
    this.setState({
        service:service
    },()=>{this.updateOrderBookData();});
  }

  updateService(){
    this.updateSmartContractData();
    this.updateOrderBookData(this.state.account);
  }

  startService(){
    this.updateSmartContractData();
    this.updateOrderBookData(this.state.account,true);
  }

  updateSmartContractData(){
    this.state.service.getWeb3Data(this.state.token,this.state.account).then((results) => {
      const [walletETH, walletToken, OtherDeltaETH, OtherDeltaToken,tradeFee,currentBlock] = results;
        //const [walletETH, walletToken, OtherDeltaETH, OtherDeltaToken] = results;

        var userTemp=this.state.user;
 
        userTemp["ethWalletBalance"]=this.state.service.toEth(walletETH, 18).toNumber().toFixed(3);
        userTemp["ethODBalance"]=this.state.service.toEth(OtherDeltaETH, 18).toNumber().toFixed(3);

        userTemp["tokenWalletBalance"]=this.state.service.toEth(walletToken, this.state.token.decimals).toNumber().toFixed(3);
        userTemp["tokenODBalance"]=this.state.service.toEth(OtherDeltaToken, this.state.token.decimals).toNumber().toFixed(3);
        userTemp["tradeFee"]=tradeFee.toNumber();
        this.setState({
            user:userTemp,
        });
    });

  }

  updateOrderBookData(user,firstTime){
    this.state.service.getOrderBookData(this.state.token,user).then((latencies) => {
        var userTemp=this.state.user;
        userTemp["orders"]={}
        userTemp["orders"]["buy"]=this.state.service.state.myOrders.buys;
        userTemp["orders"]["sell"]=this.state.service.state.myOrders.sells;
        userTemp["trades"]=this.state.service.state.myTrades;
        
        this.setState({
            user:userTemp,
            trades:this.state.service.state.trades,
            lastTrade:this.state.service.state.trades[0],
            orders:
                {buys:this.state.service.state.orders.buys,
                 sells:this.state.service.state.orders.sells},
        });
        if(this.checkOrderBookSpeed(latencies)){
            this.setOrderBookStatus("connected")
        }
        else{
            this.setOrderBookStatus("slow");
        }
    }).catch((err)=>{
        console.log(err);
        this.setOrderBookStatus("disconnected");
    });
    if(firstTime){
        setInterval(this.updateOrderBookData(user),1000);
    }

  }

  checkOrderBookSpeed(data){
    data=Object.values(data);
    const thresh=200;
    const avg=data.reduce(function(map,val){map+=val;return map})/data.length;
    return avg<=thresh;
  }

  setOrderBookStatus = (val) =>{
    this.setState({orderBookStatus:val});
  }

  onUpdateOrderSide = (val) => {
    this.setState({
      orderSide: val
    })
  };
  setGasPrice(newPrice){
    this.setState({gasPrice:newPrice.value});
    if (this.state.service!==null && this.state.service.web3!=null)
        this.state.service.setGasPrice(newPrice.value);
  }

  showAlert(msg,buttonText,isWelcome){
    if(!buttonText)
        buttonText="Ok";
    this.setState({alertModal:{show:true,text:msg,buttonText:buttonText,isWelcome:isWelcome}});
  }

  onNoPartialFillsChange(e){
        e.stopPropagation();
        this.setState({confirmTradeModal:{...this.state.confirmTradeModal,noPartialFills:e.target.checked}});
  }

  showConfirmOrder(msg,callback){
    this.setState({confirmOrderModal:{show:true,text:msg,callback:callback}});
  }

  showConfirmCancel(msg,callback){
    this.setState({confirmCancelModal:{show:true,text:msg,callback:callback}});
  }

  showConfirmTrade(generateMessage,callback){
    var [msg,initQty]=generateMessage(1.0)
    this.setState({confirmTradeModal:{show:true,text:msg,callback:callback,quantity:initQty,maxQuantity:initQty,msgGen:generateMessage,noPartialFills:false}});
  }


  handleConfirmTradeSliderStart = () => {

  }

  handleConfirmTradeSliderChange = (value) => {
    var confirmTradeModal=this.state.confirmTradeModal;
    confirmTradeModal.text=confirmTradeModal.msgGen(value/confirmTradeModal.maxQuantity)[0];
    confirmTradeModal.quantity=value;
    this.setState({confirmTradeModal:confirmTradeModal});

  }
  handleConfirmTradeTextBoxChange = (e) => {
    var value;
    if(e.target.value==="")
        value=0.0;
    else
        value=parseFloat(e.target.value);
    if(value===this.state.confirmTradeModal.quantity || isNaN(value))
        return
    this.handleConfirmTradeSliderChange(value);
  }

  handleConfirmTradeSliderComplete = () => {

  }

  calcTokenDelta(){
    var delta = NaN;
    if (this.state.trades.length>1){
        const lastTrade=this.state.trades[0];
        const firstTrade=this.state.trades.slice(-1)[0];
        delta = (lastTrade.price-firstTrade.price)/firstTrade.price;
    }
    return delta
  }

  render() {

    const confirmTradeSliderLabels={}
    confirmTradeSliderLabels[this.state.confirmTradeModal.maxQuantity]="Max Quantity";

    return (
      <div className="app_child">
        <div className="app_toast_notifications">
          <div className="toast_status">
            <div className="toast_container">
              <div className="toast_type_rgp">
                <span className={"toast_icon_sq"} status={this.state.orderBookStatus}></span>
                <div>
                  <span className="orderbook_status" status={this.state.orderBookStatus}> </span>
                 </div>
                <div>
                <Notifications ref = {instance => {this.notifier=instance;}} user ={this.state.user} token={this.state.token}></Notifications>
                </div>
              </div>
              {/*<a class="toast_more_info" href="" target="_blank" rel="noopener noreferrer">View Status</a>*/}
            </div>
          </div>
        </div>
        <Header account={this.state.account} loggedIn={this.state.mmLoggedIn} token={this.state.token} tokenList={this.state.tokenList} tokenChangeCallback={this.props.setToken} lastTrade={this.state.lastTrade} tokenDelta={this.calcTokenDelta()} gasChangeCallBack={this.setGasPrice} user={this.state.user}></Header>
        <div className="Platform">

        <ReactModal 
            isOpen={this.state.alertModal.show}
            contentLabel="Alert Modal"
            className={(this.state.alertModal.isWelcome?"Welcome":"")+"Modal"}
            overlayClassName="Overlay"
            ariaHideApp={false}
        ><div>
            {this.state.alertModal.text}
            <br />
            <br />
            <button className="btn_ok" onClick={(e)=>{this.setState({alertModal:{show:false,text:""}});}}>{this.state.alertModal.buttonText}</button>
        </div>
        </ReactModal>

        <ReactModal 
            isOpen={this.state.confirmTradeModal.show}
            contentLabel="Confirm Trade"
            className="Modal"
            overlayClassName="Overlay"
            ariaHideApp={false}
        ><div>
          <div className="heading">Confirm Trade</div>

          <div>
            {this.state.confirmTradeModal.text}
          </div>
          {!this.state.confirmTradeModal.noPartialFills &&
          <div className="slider">
              <label>Set Quantity: <input type="number" value={this.state.confirmTradeModal.quantity} onChange={this.handleConfirmTradeTextBoxChange}/></label>
              <Slider
              min={0}
              max={this.state.confirmTradeModal.maxQuantity}
              value={this.state.confirmTradeModal.quantity}
              step={0.01}
              onChangeStart={this.handleConfirmTradeSliderStart}
              onChange={this.handleConfirmTradeSliderChange}
              onChangeComplete={this.handleConfirmTradeSliderComplete}
              tooltip={false}
              labels={confirmTradeSliderLabels}
          />
          </div>}
            <button className={this.state.orderSide +' btn_confirm_trade'} onClick={(e)=>{
            this.setState({confirmTradeModal:{...this.state.confirmTradeModal,show:false,text:"",confirmed:true,callback:this.state.confirmTradeModal.callback}},()=>
                {
                this.state.confirmTradeModal.callback(this.state.confirmTradeModal.confirmed,this.state.confirmTradeModal.quantity/this.state.confirmTradeModal.maxQuantity,this.state.confirmTradeModal.noPartialFills);
                });
            }
            }>Confirm Trade</button>

            <button className="btn_cancel_trade" onClick={(e)=>{
            this.setState({confirmTradeModal:{show:false,text:"",confirmed:false,callback:this.state.confirmTradeModal.callback}},()=>
                {
                this.state.confirmTradeModal.callback(this.state.confirmTradeModal.confirmed);
                });
            }
            }>Cancel Trade</button>

        </div>
        </ReactModal>

        <ReactModal 
            isOpen={this.state.confirmOrderModal.show}
            contentLabel="Confirm Order"
            className="Modal"
            overlayClassName="Overlay"
            ariaHideApp={false}
        ><div>
          <div className="heading">Confirm Order</div>
          <div>
            {this.state.confirmOrderModal.text}
          </div>
            <button className={this.state.orderSide +' btn_confirm_trade'} onClick={(e)=>{
            this.setState({confirmOrderModal:{show:false,text:"",confirmed:true,callback:this.state.confirmOrderModal.callback}},()=>
                {
                this.state.confirmOrderModal.callback(this.state.confirmOrderModal.confirmed);
                });
            }
            }>Confirm Order</button>

            <button className="btn_cancel_trade" onClick={(e)=>{
            this.setState({confirmOrderModal:{show:false,text:"",confirmed:false,callback:this.state.confirmOrderModal.callback}},()=>
                {
                this.state.confirmOrderModal.callback(this.state.confirmOrderModal.confirmed);
                });
            }
            }>Cancel Order</button>

        </div>
        </ReactModal>

        <ReactModal 
            isOpen={this.state.confirmCancelModal.show}
            contentLabel="Confirm Cancel"
            className="Modal"
            overlayClassName="Overlay"
            ariaHideApp={false}
        ><div>
          <div className="heading">Confirm Cancel</div>
          <div>
            {this.state.confirmCancelModal.text}
          </div>
            <button className={'btn_confirm_cancel'} onClick={(e)=>{
            this.setState({confirmCancelModal:{show:false,text:"",confirmed:true,callback:this.state.confirmCancelModal.callback}},()=>
                {
                this.state.confirmCancelModal.callback(this.state.confirmCancelModal.confirmed);
                });
            }
            }>Confirm Cancel</button>

            <button className="btn_cancel_trade" onClick={(e)=>{
            this.setState({confirmCancelModal:{show:false,text:"",confirmed:false,callback:this.state.confirmCancelModal.callback}},()=>
                {
                this.state.confirmCancelModal.callback(this.state.confirmCancelModal.confirmed);
                });
            }
            }>Go Back</button>

        </div>
        </ReactModal>
        {this.state.mmLoggedIn && 
          <aside className="Sidebar">
            <div className="scroll_container trade_side">
              <div className="scroll_flex">
                <div className="PanelHeader">
                  <div className="Balance_header">Trading Balance
                    <span className="hdr_integer_caption">Available to trade</span>
                  </div>
                  <div className="PanelHeader_title"/>
                </div>
                <article>
                  <div className="BalanceInfo_container">
                    <div className="accnt_currencies">
                      <div className="currency_int">
                        <div>{this.state.user.tokenODBalance}
                          <span>
                            <span className="token_name">{this.state.token.name}</span>
                          </span>
                          <div></div>
                        </div>
                        <div className="bal_clear"/>
        <div className="hdr_integer_caption">of <span className="strong">{numeral(parseFloat(this.state.user.tokenWalletBalance)+parseFloat(this.state.user.tokenODBalance)).format(0,0.0000)} </span> {this.state.token.name}</div>
                      </div>
                      <div className="currency_int">
                        <div>{this.state.user.ethODBalance}
                          <span className="token_name">ETH</span>
                        </div>
                        <div className="bal_clear"/>
                        <div className="hdr_integer_caption">of   <span className="strong">{numeral(parseFloat(this.state.user.ethWalletBalance)+parseFloat(this.state.user.ethODBalance)).format(0,0.0000)}</span> ETH</div>
                      </div>
                    </div>
                  </div>
                </article>
                <article>
                  <div className="Module_Tradeform">
                   <div className="Trade_form">
                 <Balance user ={this.state.user} token={this.state.token} service={this.state.service} esUrl={this.state.esUrl} showAlert={this.showAlert}></Balance>
                    </div>
                    </div>
                </article>
               
                <article>
                  <div className="Module_Tradeform">
                  <PlaceOrders user ={this.state.user} token={this.state.token} service={this.state.service} showAlert={this.showAlert} showConfirm={this.showConfirmOrder} updateService={this.updateService} onUpdateOrderSide={this.onUpdateOrderSide}></PlaceOrders> 
                  </div>
                </article>
              </div>
            </div>
          </aside>
          }
          <div className="Trade_main">

            <div className="OrderBook">
              <div className="OrderBook_content">
              <div className="OrderBook_wrapper">
              <div className="OrderBook_tables_wrapper">
                <OrderBook orders={this.state.orders} user={this.state.user} service={this.state.service} esUrl={this.state.esUrl} token={this.state.token} showAlert={this.showAlert} showConfirm={this.showConfirmTrade} onUpdateOrderSide={this.onUpdateOrderSide} onNoPartialFillsChange={this.onNoPartialFillsChange}></OrderBook>
                </div>
                </div>
              </div>
            </div> 

            {/* Module_Trades */}
            <div
              className="Module_Trades"
            >

              <div className="PanelHeader">
                <div className="PanelHeader_group">
                  <div className="Orders_header">{this.state.mmLoggedIn?"My Activity":""}
                  </div>
                  <div className="PanelHeader_title"/> {/*will change to: My Trades | All (Recent) Trades  */}</div>
              </div>
            {this.state.mmLoggedIn?(<UserTransactions user ={this.state.user} token={this.state.token} tokenFromAddr={this.props.tokenFromAddr} service={this.state.service} esUrl={this.state.esUrl} showAlert={this.showAlert} showConfirm={this.showConfirmCancel}/>)
            :this.state.web3===null?<a href="https://metamask.io/"><img src={downloadMM} alt="Download MetaMask" style={{marginLeft:"100px"}}/></a>:""}
            </div>
            {/* /Module_Trades */}
            <div className="Module_Orders">
              <div className="Order_panel">
                <div className="Order_panel_inner">
                  <div className="PanelHeader">
                    <div className="PanelHeader_group">
                      <div className="Orders_header">Recent Trades {/* Orders */}
                      </div>
                      <div className="PanelHeader_title"/>{/*will be orders| Token | Open | All */}
                    </div>
                  </div>
                  <TradeHistory trades = {this.state.trades} token={this.state.token} ></TradeHistory> 
                  <Gitter room="UberDelta/Lobby" title="Char more" />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

    /* end */
    );
  }
}

export default AppGrid;



