import React, { Component } from 'react';
import logo from "../../uberdelta-logo.svg";

class LandingPage extends Component {

    render() {

		const mainStyle={
			border: "2px solid #99ce32",
			margin: "5% auto",
			width:"600px",
			background: "#f1f1f1",
			padding: "20px",
            textAlign:"center"
		}

		const descriptionStyle={
			fontSize:"20px"
		};

        const headingStyle={
            fontWeight:"700px",
            fontSize:"40px",

        }

        return(
            <div style={mainStyle}>
            <img src={logo} alt="UberDelta logo"/>
			<br />
			<br />
            <div style={headingStyle}>Welcome to UberDelta!</div>
            <br />
            <div style={descriptionStyle}>
                UberDelta is a decentralized exchange. If you have used other popular Ethereum-based exchanges, you will find UD simple and easy to use.
            </div>
			</div>
            );
    }
}
export default LandingPage;



