import React, {Component} from 'react';
import ReactDOM from 'react-dom';
import AppGrid from './Application.js'
import {BrowserRouter, Route} from 'react-router-dom';
import config from './config.json';
import LandingPage from './components/LandingPage'
import Web3 from 'web3';
import Error404 from './components/Error404';
import WelcomePage from './components/WelcomePage';
import { Redirect } from 'react-router'
const ABIERC20Token = require('./contracts/token.json');

class RouteChecker extends Component{

    constructor(props,context){

        super(props,context);

        this.state = {
            validView:false,
            token:null
        };

        this.updateRoute=this.updateRoute.bind(this);
        this.setToken=this.setToken.bind(this);
    }

    
    setToken(newTokenData){
        const priorToken=this.state.token;
        const newToken=this.state.tokenList.filter(token => (token.name===newTokenData.value))[0];
        if(newToken!==priorToken)
        { 
            this.props.history.push("/token/"+(newToken.unlisted?newToken.addr:newTokenData.value.toLowerCase()));
        }
    }

    handleLocationChange = (location) => {
    }

    componentWillReceiveProps(nextProps){
        this.updateRoute(nextProps.match.params.token);
    }


    componentDidMount(){

    this.unsubscribeFromHistory = this.props.history.listen(this.handleLocationChange);

        config.tokens=config.tokens.map(function(obj){
            if(!obj.hasOwnProperty('tokenType')){
                obj["tokenType"]=20;
            }
            return obj
        },{});

        var web3=null;
        if (typeof window.web3 !== 'undefined') {
          // eslint-disable-next-line no-undef
            web3 = new Web3(window.web3.currentProvider);
        }

        this.setState({web3:web3});
        var tokenDict = config.tokens.reduce(function(map, obj) {
            map[obj.name] = obj;
            return map;
        }, {});

        var tokenAddrDict = config.tokens.reduce(function(map, obj) {
            map[obj.addr.toLowerCase()] = obj;
            return map;
        }, {});

        this.setState({tokenList:config.tokens,tokenAddrDict:tokenAddrDict,tokenDict:tokenDict},()=>{this.updateRoute(this.props.match.params.token)});
    }

    updateRoute(rawToken){
        var urlToken;
        if (typeof rawToken!=="undefined"){
            urlToken=rawToken.toUpperCase();
        }
        else{
            urlToken=config.defaultToken.toUpperCase();
        }
        if (this.state.tokenDict.hasOwnProperty(urlToken)){
            this.setState({validView:true,token:this.state.tokenDict[urlToken]});
        }
        else if (this.state.web3.isAddress(rawToken)){
            const tokenContract = this.state.web3.eth.contract(ABIERC20Token).at(rawToken);
            var data=[];
            tokenContract.name((err,res)=>{
                data.push(res);
                tokenContract.decimals((err,res)=>{
                    data.push(res);
                    tokenContract.name((err,res)=>{
                        data.push(res);
                        const token={addr:rawToken,name:rawToken.slice(2,8),decimals:data[1].toNumber(),tokenType:20,unlisted:true};
                        var temp=this.state.tokenAddrDict;
                        temp[token.addr.toLowerCase()]=token;
                        config.tokens.push(token);
                        this.setState({validView:true,token:token,tokenAddrDict:temp,tokenList:config.tokens});
                    });
                });
            });
        }


    }
    
    render() {
        var renderVal=<Error404 history={this.props.history} />
        if (this.state.validView){
            renderVal=<AppGrid token={this.state.token} tokenList={this.state.tokenList} addressOtherDelta={config.addressOtherDelta} history={this.props.history} esUrl={config.etherscanUrl} tokenFromAddr={this.state.tokenAddrDict} web3={this.state.web3} setToken={this.setToken}></AppGrid>;
        }
        return (renderVal);
    }
}
// eslint-disable-next-line
const history = history;
ReactDOM.render(
    <BrowserRouter>   
         <div className="app_root">
        <Route path="/token/:token" component ={RouteChecker} history={history}/>
        <Route exact path="/token/" component ={RouteChecker} history={history}/>
        <Route exact path="/welcome/" component ={WelcomePage} history={history}/>
        <Route exact path="/" render ={() => (<Redirect to="/token/" />)} history={history}/>
        </div>
    </BrowserRouter>,
    document.getElementById('root')
);




