import React, { Component } from 'react';
import logo from "../../uberdelta-logo.svg";

class LandingPage extends Component {

    render() {

		const mainStyle={
			border: "2px solid #99ce32",
			margin: "5% auto",
			width:"600px",
			background: "#f1f1f1",
			padding: "20px",
		}

		const descriptionStyle={
			fontSize:"20px"
		};

        return(
            <div style={mainStyle}>
            <img src={logo} alt="UberDelta logo"/>
			<br />
			<br />
            <font style={descriptionStyle}>
			UberDelta is an decentralized exchanged built upon the Ethereum blockchain launching in <b>February 2018</b>.<br />Our publically available and independently-audited smart contract includes features such as:
			</font>
				<ul>
					<li>Lower gas consumption than other popular decentralized exchanges</li>
					<li>Complete security of user funds as user remains in total control of deposit/withdraw functionality</li>
					<li>Built-in upgrade functionality</li>
					<li>Low trading fees and increased discounts during launch period</li>
				</ul>
            <font style={descriptionStyle}>
			Additional features of the exchange:
			</font>
				<ul>
					<li>Fast off-chain orderbook service</li>
					<li>Dedicated support team</li>
				</ul>
			<link href="//cdn-images.mailchimp.com/embedcode/slim-10_7.css" rel="stylesheet" type="text/css" />
			<div id="mc_embed_signup">
			<form action="https://uberdelta.us17.list-manage.com/subscribe/post?u=2d3842e4a767b41ce4f094c6a&amp;id=33da2f33e4" method="post" id="mc-embedded-subscribe-form" name="mc-embedded-subscribe-form" className="validate" target="_blank" noValidate>
				<div id="mc_embed_signup_scroll">
				<label forhtml="mce-EMAIL">Subscribe to our mailing list to be the first to hear of news and announcements</label>
				<input type="email" defaultValue="" name="EMAIL" className="email" id="mce-EMAIL" placeholder="email address" required />
				<div style={{position:"absolute", left: "-5000px"}} aria-hidden="true"><input type="text" name="b_2d3842e4a767b41ce4f094c6a_33da2f33e4" tabIndex="-1" defaultValue="" /></div>
				<br />
				<div className="clear"><input type="submit" value="Subscribe" name="subscribe" id="mc-embedded-subscribe" className="button" /></div>
				</div>
			</form>
			</div>
			</div>
            );
    }
}
export default LandingPage;



