import React from 'react';


export function new_alert_details(type,title,heading,text,link){
    //link is expected to be 2 element array: 0:link target 1: link text
    var out = <div className="opening_body" key="0">
    <div className="alert_logo"></div>
    <div className="alert_title">
    {title} 
    </div>
    <div className="alert_heading">
    {heading}
    </div>
    <div className="alert_text" dangerouslySetInnerHTML={{__html:text}}>
    </div>
    {link &&
        <div className="alert_link"><a href={link[0]} target="_blank">{link[1]}</a></div>
    }
    </div>
    return out;
}

export function mail_chimp_signup(){
    var out=
        <div className="alert_link" key="1">
        Additionally, please 
        <a href="https://uberdelta.us17.list-manage.com/subscribe/post?u=2d3842e4a767b41ce4f094c6a&amp;id=33da2f33e4" target="_blank" rel="noopener noreferrer"> subscribe</a > to our mailing list to hear updates and news as fast as possible
        </div>
    return out;
}



