import React, { Component } from 'react';
import './style.css';
import Table from "../../Table";

import {
    Row,
    Col,
} from 'react-bootstrap';

import numeral from 'numeral';
import _ from 'underscore';
import uid from 'node-uuid';
import Web3Utility from "../../../utility.js";

var dateFormat = require('dateformat');

class OrdersTable extends Component {

    constructor(props, context) {
        super(props, context);

        this.state = {
                orders: []
        };

        this.handleCancel = this.handleCancel.bind(this);

    }

    handleCancel = (clickedOrder) => {
        var msg;
        const takerTokenAddress = this.props.tokenFromAddr[clickedOrder.takerTokenAddress]
        const makerTokenAddress = this.props.tokenFromAddr[clickedOrder.makerTokenAddress]
        const takerTokenAddressDivisor=Web3Utility.tokenDivisor(takerTokenAddress);
        const makerTokenAddressDivisor=Web3Utility.tokenDivisor(makerTokenAddress);
        msg=
        <div>
        <div className="cancel_message">
        {"Are you sure that you want to cancel the following order?"}
        </div>
        <div className="cancel_details">
        Receiving <strong>{Web3Utility.weiToEth(clickedOrder.takerTokenAmount,takerTokenAddressDivisor)}</strong> {takerTokenAddress.name} <strong>@{clickedOrder.price.toFixed(6)}</strong> for giving <strong>{Web3Utility.weiToEth(clickedOrder.makerTokenAmount,makerTokenAddressDivisor)}</strong> {makerTokenAddress.name}
        </div>
        <br />
        </div>
        //eslint-disable-next-line 
        this.props.showConfirm(msg,(confirmed)=>{
            if(!confirmed)
                return;
            this.props.service.cancelOrder(this.props.user.addr,clickedOrder).then ((result)=> {
                const esLink = "{}tx/{}".format(this.props.esUrl,result);
                console.log(esLink);
                this.props.showAlert(
                    <div>{"Successfully sent cancel tx. TX hash: {}".format(result)}
                    <br />
                    <a href={esLink} target="_blank">EtherScan Link</a>
                    </div>
                    );
              }, (err)=> {
                this.props.showAlert("Unable to send cancel tx with error: {}".format(err));
            }); 
        });
    }

    render() {
        const renderOrders = (orders,token) => {

            const renderSellOrBuy = (data,token,side) => {
                var todo=null
                if (side==="sell"){
                    todo=_.sortBy(data.sell, function (x){return -x.price;});
                }else if (side==="buy"){
                    todo=_.sortBy(data.buy, function (x){return x.price;});
                }
                return (_.map(todo, (order) => (
                        <tr className={side} key={uid.v4()}>
                            <td className="text-side">
                                {side}
                            </td>
                            <td>
                                {numeral(order.price).format('0.'+"0".repeat(6)) }
                            </td>
                            <td>
                                {numeral(Web3Utility.weiToEth(order.amountRemaining)*order.price).format("0."+"0".repeat(4))}
                            </td>
                            <td>
                                {numeral(Web3Utility.weiToEth(order.amountRemaining)).format("0."+"0".repeat(4))}
                            </td>
                            <td>
                                {dateFormat(order.date,"HH:MM:ss yyyy-mm-dd") }
                            </td>
                            <td>
                                <button type="button" onClick={()=>{this.handleCancel(order)}}>Cancel</button>
                            </td>
                        </tr>
                )));
            };

            const renderOrdersList = (data, token) => (
                <Table
                    condensed
                    hover
                    className= "tradesTable"
                >
                    <thead>
                        <tr>
                            <th>Side</th>
                            <th>Price</th>
                            <th>ETH</th>
                            <th>{token.name}</th>
                            <th>Time Submitted</th>
                            <th>Cancel</th>
                        </tr>
                    </thead>
                    <tbody>
                        {renderSellOrBuy(data,token,"sell")}
                        <tr></tr>
                        {renderSellOrBuy(data,token,"buy")}
                    </tbody>
                </Table>
            );

    return (
        <Row className="orders">
            <Col lg={ 12 }>
                { renderOrdersList(orders,token) }
            </Col>
        </Row>
    );
}


        return (
            <div className="mainWrap">
                <Row>
                    <Col md={ 12 }>
                        { renderOrders(this.props.orders,this.props.token) }
                    </Col>
                </Row>
            </div>    
        );
    }
}
export default OrdersTable;



