import React, { Component } from 'react';
import ReactResponsiveSelect from 'react-responsive-select';
import './style.css';

const caretIcon = (
    <svg className="caret-icon" x="0px" y="0px" width="11.848px" height="6.338px" viewBox="351.584 2118.292 11.848 6.338">
      <g><path d="M363.311,2118.414c-0.164-0.163-0.429-0.163-0.592,0l-5.205,5.216l-5.215-5.216c-0.163-0.163-0.429-0.163-0.592,0s-0.163,0.429,0,0.592l5.501,5.501c0.082,0.082,0.184,0.123,0.296,0.123c0.103,0,0.215-0.041,0.296-0.123l5.501-5.501C363.474,2118.843,363.474,2118.577,363.311,2118.414L363.311,2118.414z"/></g>
    </svg>
  );
  

class Gas extends Component {

  constructor(props,context){
    super(props,context);

    this.state={options:[]};

    fetch("https://gasprices.uberdelta.com/")
    .then(function(response){
        return response.json();
    }).then((data)=>{
        const gasPrices={
                "Safe":data["safeLow"]/10.0,
                "Average":data["average"]/10.0,
                "Fast":data["fastest"]/10.0
        };
        const makeOptions=(gasPrices)=>{
            var out=[];
            const makeElement=(key,val)=>{
                return {value:val,text:val,markup:<span> {val}<span className="hdr_integer_caption" aria-hidden="true">Gwei - {key}</span></span>}
            };
            for (var key in gasPrices){
                out.push(makeElement(key,gasPrices[key]));
            }
            return out
        };
        this.setState({gasPrices:gasPrices,options:makeOptions(gasPrices)});
        this.props.gasChangeCallBack(gasPrices["Safe"]);
 
       },()=>{
            console.log("done updating gasPrices"); 
            }
    ).catch(function(err){
        console.log(err);
    });
    
  }

  reportChange(newValue) {
    console.log(newValue);
  }

  render() {
    if(this.state.options.length>0){ 
        return (
          <form>

            <ReactResponsiveSelect
              name="make"
              options={this.state.options}
              onChange={this.props.gasChangeCallBack}
              caretIcon={caretIcon}
              prefix=""
            />
            
          </form>
        );
    }
    else
        return null;
  }
}

export default Gas;



