import React, { Component } from 'react';
import Token from "./Token";
import Gas from "./Gas";
import './style.css';
var numeral = require('numeral');

class Header extends Component {

    constructor(props, context) {
      super(props, context);
      this.state={

        token:this.props.token,
      };
      
  
  }

    render() {
        var accountVal="NO METAMASK ACCOUNT LOGGED IN";

        if (this.props.loggedIn)
        {
            accountVal=this.props.account
        }

        return (
            <div className="header_nav">
            <div className="menu">
              <a className="uberdelta-logo" title="UBERDELTA">
                <span />
              </a>
              <div className="navbar_wrapper">
                <ul className="hdr_integer_group">
                  <li className="hdr_token_selector">
                  <h4 className="hdr_counter">
                      <Token  token={this.props.token} tokenList={this.props.tokenList} tokenChangeCallback={this.props.tokenChangeCallback}/>
                      <span className="hdr_integer_caption_dd">Select Token</span>
                    </h4>
                  </li>
                  <li>
                    <h4 className="hdr_counter">
                      <span className="hdr_integer">{typeof this.props.lastTrade==="undefined"?NaN:this.props.lastTrade.price} ETH/{this.props.token.name}</span>
                      <span className="hdr_integer_caption">Last trade price</span>
                    </h4>
                  </li>
                  <li className={this.props.tokenDelta<=0?"hdr_change_neg":"hdr_change_pos"}>
                    <h4 className="hdr_counter">
                      <span className="hdr_integer" />
                      <span className="hdr_integer">{numeral(this.props.tokenDelta*100).format("+0,0.00")}%</span>
                      <span className="hdr_integer_caption">24 hrs</span>
                    </h4>
                  </li>
                  <li className="hdr_gas">
                    <h4 className="hdr_counter">
                      <Gas  gasChangeCallBack={this.props.gasChangeCallBack}/>
                      <span className="hdr_integer_caption_dd">Gas Price (Gwei)</span>
                    </h4>
                  </li>
                </ul>
              </div>
              <div className="hdr_clear"  />
              <div className="hdr-ethbalance"><span></span>  {accountVal.indexOf("METAMASK")>-1?accountVal:accountVal.slice(0,10)+"... "+this.props.user.ethWalletBalance+" ETH"} </div>
            </div>
          </div>
            
            
        );
    }
}
export default Header;



