import React, { Component } from 'react';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import './style.css';
//import 'react-tabs/style/react-tabs.css';
import numeral from 'numeral';
//var format = require('string-format');
import * as utils from "../../utils.js";
import BigNumber from "bignumber.js";

class PlaceOrders extends Component {

    constructor(props, context) {
        super(props, context);
        const ethQuantity = numeral(0).format("0."+"0".repeat(6));

        const defaultBuyTokenQuantity="Quantity {} to buy".format(this.props.token.name);
        const defaultSellTokenQuantity="Quantity {} to sell".format(this.props.token.name);

        this.state={

            inputs:{
                buy:{
                        tokenQuantity:defaultBuyTokenQuantity,
                        ethQuantity: ethQuantity,
                        price : "Price",
                        expires: 10000,
                        defaultTokenQuantity:defaultBuyTokenQuantity
                },
                sell:{
                        tokenQuantity:defaultSellTokenQuantity,
                        ethQuantity: ethQuantity,
                        price : "Price",
                        expires: 10000,
                        defaultTokenQuantity:defaultSellTokenQuantity
                }
            },
            orderSide: "",
            tradeFee:this.props.user.tradeFee
        }
        this.handlePlaceOrder = this.handlePlaceOrder.bind(this);
        this.setVal = this.setVal.bind(this);
    }

    componentWillReceiveProps(nextProps){
        this.setState({tradeFee:nextProps.user.tradeFee});
    }

    handleChange = (id1,id2) => e => {
        this.setVal(id1,id2,e.target.value);
    }

    setVal = (id1,id2,val) =>{
        var tempInputs= this.state.inputs;
        tempInputs[id1][id2]= val;
        if(id2.indexOf("tokenQuantity")!==-1 || id2.indexOf("price")!==-1){
            var tmp = tempInputs[id1]
            tempInputs[id1]["ethQuantity"]=numeral(tmp["price"]*tmp["tokenQuantity"]).format("0."+"0".repeat(6))
        }
        this.setState({inputs:tempInputs});
    }

    handlePlaceOrder = (side) => e => {
        e.preventDefault();
        const inputs = this.state.inputs[side];
        const props=this.props;
        if(isNaN(inputs.price)||isNaN(inputs.tokenQuantity)||isNaN(inputs.expires)){
            this.props.showAlert(utils.new_alert_details(
                                "error",
                                "Error",
                                "Your order was not placed.",
                                "There was an error processing the order values. Please make sure you are submiting valid numerical data"
            ));
            return;
        }
        const price=BigNumber(inputs.price);
        const quantity=BigNumber(inputs.tokenQuantity);
        const expiresDelta = parseInt(inputs["expires"],10);
        const expires = props.service.state.currentBlock+expiresDelta;
        this.setState({orderSide:side});
        this.props.onUpdateOrderSide(side);
        //var msg="Are you sure you want to submit the following order?\n\tSide: {}\n\tPrice: {}\n\tQuantity: {}\n\tExpires on Block: {}".format(side,price.toFixed(9),inputs.tokenQuantity,"{} ({} blocks after current)".format(expires,expiresDelta));
        var msg="Are you sure you want to submit the following {}\n\t order?".format(side);
        var tot=quantity.times(price).toNumber();
        var prc =price.toNumber();
        var sideu = side.replace(/\b\w/g, l => l.toUpperCase())

        msg=
        <div>
            <div className="trade_message">
            {msg}
            </div>
            <div className="trade_details">
                Quantity: <span className="amt">{quantity.toNumber()}</span> {this.props.token.name} 
    <div> </div>
            <span className={side}><span className="cnvbuy">@{sideu} Price: <span className="amt">{prc}</span>  {this.props.token.name}/ETH</span> </span> <span className="amt">{tot.toFixed(4)} </span> ETH
            <div> </div>
                <div className="text">  
                    Expires on Block <b>{expires}</b> ({expiresDelta} blocks after current)<br />
                </div> 
            </div>
        </div>

        //eslint-disable-next-line
        this.props.showConfirm(msg,(confirmed)=>{
            if (!confirmed)
                return;
            console.log("creating order");
            props.service.createOrder(side, expires, price, quantity, props.token, props.user, (err,order) =>
            {
                if(!err)
                {
                    const handleError=(err)=>{
                        const msg="There was an error submitting your order: ({}). Please verify that you are submitting a valid order.".format(err);
                        this.props.showAlert(utils.new_alert_details(
                                            "error",
                                            "Error",
                                            "Your order was not placed.",
                                            msg
                        ));
                    };
                    props.service.placeOrder(order).then((result) => {
                        if(result["result"]==="success"){
                            msg="You successfully placed an order on the orderbook!";
                            this.props.showAlert(utils.new_alert_details(
                                                "success",
                                                "Success",
                                                "Order Placed",
                                                msg
                            ));
                            this.setVal(side,"tokenQuantity",0);
                            this.setVal(side,"price",0);
                            this.setVal(side,"expires",10000);
                            setTimeout(()=>{
                                this.props.updateService();
                            },5000);
                        }
                        else{
                            handleError(result["error"]);
                        }
                      }, (err)=>{handleError(err)});
                }
                else
                {
                    this.props.showAlert(err);
                }
            });
        });
    }

    render() {


        const renderTable = (token,input,type) =>(
            <div className="Trade_form">
            <div className="market-order">

              <div className="input_order_grp">
                <div className="input_order_box">
                    <input type="number" placeholder={input.defaultTokenQuantity} value={input.tokenQuantity!==input.defaultTokenQuantity?input.tokenQuantity:""} onChange={this.handleChange(type,"tokenQuantity")} />
                  <div className="max_buy">
                    <div className="btn_buy_max">Buy Max</div>
                  </div>
                  <span>Quantity {token.name}</span>
                </div>
              </div>

              <div className="input_order_grp">
                <div className="input_order_box">
                    <input type="number" value={input.ethQuantity}  readOnly/>
                  <span>ETH</span>
                </div>
              </div>

              <div className="input_order_grp">
                <div className="input_order_box">
                    <input type="text" placeholder={"Price"} value={input.price!=="Price"?input.price:""} onChange={this.handleChange(type,"price")} />
                  <div className="max_buy">
                    <div className="btn_buy_max">Buy Max</div>
                  </div>
                  <span>ETH/{token.name}</span>
                </div>
              </div>

              <div className="input_order_grp">
                <div className="input_order_box">
                    <input type="number" value={input.expires} onChange={this.handleChange(type,"expires")} />
                  <div className="max_buy">
                    <div className="btn_buy_max">Buy Max</div>
                  </div>
                  <span>Expires (Blocks)</span>
                </div>
              </div>

   

            </div>
         
            {/*
            <div className="order_total">

              <div>
                <b>UberDelta Fee</b>
                <span>(ETH)</span>
                <b></b>
              </div>
              <div className="order_fee_int">{100*tradeFee+"%"}</div>
        
            </div>
            < div className = "order_total" > <div>
                <b>Total</b>
                <span>(ETH)</span>
                <b></b>
            </div> < div className = "order_total_int" >{(input.ethQuantity*(1+tradeFee)).toFixed(6)}< /div>

            </div >
            */}
            <br />

            <input type="submit"  className={type==="sell" ? "btn_order_sell" :"btn_order"} value={type==="sell" ? "Sell {} token".format(token.name) :"Buy {} token".format(token.name)} onClick={this.handlePlaceOrder(type)}/>
        {/*
            <div className="order_validation">
              <span/>
              <div>Validation message</div>
            </div>
        */}
          </div>
        );

        const component = (

            
                <Tabs>
                    <TabList>
                        <Tab>BUY</Tab>
                        <Tab>SELL</Tab>
                    </TabList>

                    <TabPanel>
                        {renderTable(this.props.token,this.state.inputs.buy,"buy")}
                    </TabPanel>
                    <TabPanel>
                        {renderTable(this.props.token,this.state.inputs.sell,"sell")}
                    </TabPanel>
                </Tabs>
    
        );
        return (component);
    }
}
export default PlaceOrders;



