import React, { Component } from 'react';
import './style.css';

import classes from './ExchangeAndTrading.scss';
import './ExchangeAndTrading.css';
import Table from "../Table";

import {
    Row,
    Col,
} from 'react-bootstrap';

import numeral from 'numeral';
import _ from 'underscore';
import uid from 'node-uuid';
import Web3Utility from "../../utility.js";

var dateFormat = require('dateformat');

class OrderBook extends Component {

    constructor(props, context) {
        super(props, context);

        this.state = {
            orders:{
                buyOrders: [],
                sellOrders: [],
                allOrders: []
            },
            noPartialFills:false,
            orderSide: "",
            tradeFee:this.props.user.tradeFee
        };

        this.onClick = this.onClick.bind(this);
    }

    componentWillReceiveProps(nextProps) {
        this.setState({ data: nextProps.data,tradeFee:nextProps.user.tradeFee });  
      }

    onClick = (type,id) => e => {


        const orders=this.props.orders;
        const clickedOrder= orders[type][id];
        var msg;
        var tkn, amt, amtEth, prc,execQty,execType;
        const tokenDivisor=Web3Utility.tokenDivisor(this.props.token);


        tkn=this.props.token.name;
        prc=(parseFloat((parseFloat(clickedOrder.price.toString())).toFixed(15)))

        const generateMessage = (orderFraction) => {
            const amountRemainingToken=clickedOrder.amountRemaining*Math.min(1.0,orderFraction);
            const amountRemainingEth=clickedOrder.amountRemainingEth*Math.min(1.0,orderFraction);
            amt=Web3Utility.weiToEth(amountRemainingToken,tokenDivisor);
            amtEth=Web3Utility.weiToEth(amountRemainingEth);
            if(type==="buys") {
                this.props.onUpdateOrderSide(type);
                msg="Are you sure that you want to take the following Selling action?";
                execQty=Web3Utility.weiToEth(amountRemainingToken*(1.0+this.props.user.tradeFee),tokenDivisor);
                execType=this.props.token.name;
                } 
               else {
                this.props.onUpdateOrderSide(type);
                msg="Are you sure that you want to take the following Buying action?";
                execQty=Web3Utility.weiToEth(amountRemainingEth*(1.0+this.props.user.tradeFee));
                execType="ETH";
               }
            msg=<div>
            <div className="trade_message">{msg}</div>
                <div className="trade_details">
                    <span className="amt">{amt.toFixed(4)}</span> {tkn}  
                    <span className={type}><span className="cnvbuy"> @<span className="amt">{prc} </span>  ETH/{tkn}</span></span> 
                    <span className="amt"> {amtEth.toFixed(4)}</span> ETH
                </div>
                <br />
                <span>Trade fee: {this.props.user.tradeFee*100}%</span>
                <br />
                <span>Total execution quantity (inclusive of fee):<span style={{fontWeight:"1000"}}> {execQty} {execType}</span></span>
            <div className="order_input"> 
                <input type="checkbox" id="noPartialFills" name="noPartialFills" onChange={this.props.onNoPartialFillsChange}/>
            <label htmlFor="noPartialFills">Only allow execution with full quantity (No partial fills)</label>
            </div>
            </div>
            return [msg,amt];
        }
        this.props.showConfirm(generateMessage,(confirmed,confirmedQtyFrac,noPartialFills)=>{
            if(!confirmed)
                return;
            this.props.service.takeOrder(this.props.user.addr,clickedOrder,confirmedQtyFrac,noPartialFills).then ((result)=> {
                const esLink = "{}tx/{}".format(this.props.esUrl,result);
                console.log(esLink);
                this.props.showAlert(
                    <div>{"Successfully sent trade tx. TX hash: {}".format(result)}
                    <br />
                    <a href={esLink} target="_blank">EtherScan Link</a>
                    </div>);
              },(err) => {
                this.props.showAlert(
                    <div>
                    <div className="heading">Failed Transaction</div>
                    <div className="text">{"Unable to send trade tx with error: {}".format(err)}</div>
                    </div>);
            }); 
        });
    }

    render() {
        const tradeFee=isNaN(this.state.tradeFee)?0.0:this.state.tradeFee;

        const renderOrders = (orders) => {

        const renderOrdersList = (data,side) => (
            <Table
                condensed
                hover
                className= "ordersTable"
                >
                <thead>
                    <tr>
                        <th>Price</th>
                        <th>Amount</th>
                        <th>Updated</th>
                        {/*<th></th> */}
                    </tr>
                </thead>
                <tbody>
                    {_.map(data, (order,orderIndex) => (
                        <tr key={uid.v4()} onClick={this.onClick(side,orderIndex)}>
                            <td className="text-side">
                                { numeral(order.price.toString()).format('0,0.0000') }
                            </td>
                            <td>
                                { Web3Utility.weiToEth(order.amountRemaining,Math.pow(10,this.props.token.decimals)).toString() }
                            </td>
                            <td>
                                { dateFormat(order.date,"HH:MM:ss yyyy-mm-dd ") }
                            </td>
                        </tr>
                    ))}
                </tbody>
            </Table>
        );

        return (
            <Row className="orders">
                <Col lg={ 12 } className={'sells'}>
                <div className={'PanelHeader'}>
                  <div className={'Balance_header'}>
                        <i className={`fa fa-fw fa-download ${classes.iconSell}`}></i>
                        { ' Orders to Sell' }  <span className="hdr_caption"> click to Buy</span>
                        </div>
                </div>
                    { renderOrdersList(orders.sells,"sells") }
                </Col>
                <Col lg={ 12 } className={'buys'}>
                <div className={'PanelHeader'}>
                  <div className={'Balance_header'}>
                        <i className={`fa fa-fw fa-download ${classes.iconBuy}`}></i>
                        { ' Orders to Buy' } <span className="hdr_caption"> click to Sell</span>
                        </div>
                </div>
                    { renderOrdersList(orders.buys,"buys") }
                </Col>
            </Row>
        );
    }


        let orders = null;
        if (typeof this.props.orders !== 'undefined')
        {
            this.props.orders.buys.sort((a,b)=>{return(b.price-a.price)});
            this.props.orders.sells.sort((a,b)=>{return(b.price-a.price)});
            orders = renderOrders(this.props.orders);
        }
        return (
            <div className="mainWrap">
                {orders}
            </div>    
        );
    }
}
export default OrderBook;



