import React, { Component } from 'react';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import './style.css';
//import 'react-tabs/style/react-tabs.css';
import TradesTable from "../TradesTable";
import OrdersTable from "./OrdersTable";

class UserTransactions extends Component {

    constructor(props, context) {
        super(props, context);
        this.state={

            inputs:{
                trades:{
                        tokenAmount:"Amount",
                        ethAmount: "Amount"
                },
                orders:{
                        tokenAmount:"Amount",
                        ethAmount: "Amount"
                },
                funding:{
                        tokenAmount:"Amount",
                        ethAmount: "Amount",
                        tokenAddress : "Address",
                        ethAddress : "Address"
                }
            }
        }
    }

    render() {

        var tradesTab = [];
        var ordersTab = [];
        var fundingTab = [];

        tradesTab.push((
            <tr>
            </tr>
        ));

        ordersTab.push((
            <tr>
            </tr>
        ));

        fundingTab.push((
            <tr>
            </tr>
        ));

        const component = (
            <Tabs>
                <TabList>
                    <Tab>TRADES</Tab>
                    <Tab>ORDERS</Tab>
                </TabList>

                <TabPanel>
                    <TradesTable token={this.props.token} trades={this.props.user.trades}></TradesTable>
                </TabPanel>
                <TabPanel>
                    <OrdersTable token={this.props.token} orders={this.props.user.orders} tokenFromAddr={this.props.tokenFromAddr} service={this.props.service} user={this.props.user} esUrl={this.props.esUrl} showAlert={this.props.showAlert} showConfirm={this.props.showConfirm}></OrdersTable>
                </TabPanel>
            </Tabs>
        );
        return (component);
    }
}
export default UserTransactions;



